import * as config from '../Config';

var post = function (url, data, success, failure) {
  var xhr = new XMLHttpRequest();
  xhr.timeout = data.timeout || config.HTTP_CONNECT_TIMEOUT;
  xhr.open("POST", url, true);
  xhr.setRequestHeader("Content-type", "application/json; charset=utf-8");
  xhr.onload = function (e) {
    success && success(xhr.responseText);
  };
  xhr.onerror = function (e) {
    failure && failure(e, url);
  };
  xhr.ontimeout = function (e) {
    failure && failure(e, url);
  }
  xhr.send(JSON.stringify(data));
};

var shouldUseHttps = function() {
  return "https:" == document.location.protocol ? true : false;
}

export { post, shouldUseHttps };


