import {isChrome, isFireFox, isSafari} from './utils/Browser'

var simMap = function(profile) {
  var resolution;

  switch (profile) {
    case "120p":
    case "120p_1":
      resolution = ["120p_1", "120p_1", "120p_1"];
      break;

    case "120p_3":
      resolution = ["120p_3", "120p_3", "120p_3"];
      break;

    case "180p":
    case "180p_1":
      resolution = ["90p_1", "90p_1", "180p_1"];
      break;

    case "180p_3":
      resolution = ["120p_3", "120p_3", "180p_3"];
      break;

    case "180p_4":
      resolution = ["120p_1", "120p_1", "180p_4"];
      break;

    case "240p":
    case "240p_1":
      resolution = ["120p_1", "120p_1", "240p_1"];
      break;
    
    case "240p_3":
      resolution = ["120p_3", "120p_3", "240p_3"];
      break;

    case "240p_4":
      resolution = ["120p_4", "120p_4", "240p_4"];
      break;

    case "360p":
    case "360p_1":
    case "360p_4":
    case "360p_9":
    case "360p_10":
    case "360p_11":
      resolution = ["90p_1", "90p_1", "360p_1"];
      break;

    case "360p_3":
    case "360p_6":
      resolution = ["120p_3", "120p_3", "360p_3"];
      break;
    
    case "360p_7":
    case "360p_8":
      resolution = ["120p_1", "120p_1", "360p_7"];
      break;

    case "480p":
    case "480p_1":
    case "480p_2":
    case "480p_4":
    case "480p_10":
      resolution = ["120p_1", "120p_1", "480p_1"];
      break;

    case "480p_3":
    case "480p_6":
      resolution = ["120p_3", "120p_3", "480p_3"];
      break;

    case "480p_8":
    case "480p_9":
      resolution = ["120p_4", "120p_4", "480p_8"]; 
      break;
      
    case "720p":
    case "720p_1":
    case "720p_2":
    case "720p_3":
      resolution = ["90p_1", "90p_1", "720p_1"]; 
      break;

    case "720p_5":
    case "720p_6":
      resolution = ["120p_1", "120p_1", "720p_5"]; 
      break;

    case "1080p":
    case "1080p_1":
    case "1080p_2":
    case "1080p_3":
    case "1080p_5":
      resolution = ["90p_1", "90p_1", "1080p_1"]; 
      break;

    case "1440p":
    case "1440p_1":
    case "1440p_2":
      resolution = ["90p_1", "90p_1", "1440p_1"]; 
      break;
    
    case "4k":
    case "4k_1":
    case "4k_3":
      resolution = ["90p_1", "90p_1", "4k_1"];
      break;

    default:
      resolution = ["120p_1", "120p_1", "360p_7"];
  }
  
  if(isFireFox()) {
    return [resolution[1], 30, 100];
  } else if(isSafari()) {
    return [resolution[2], 15, 100];
  } else {
    return [resolution[0], 15, 100];
  }
}

export {
  simMap
}




