import {getTimestamp} from './Time'


/*
 * API to write logs based on traditional logging mechanisms: debug, trace, info, warning, error
 */
var Logger = (function () {
  "use strict";
  var DEBUG = 0, INFO = 1, WARNING = 2, ERROR = 3, NONE = 4, logLevel = DEBUG, setLogLevel, log, debug, info, warning, error;

  // It sets the new log level. We can set it to NONE if we do not want to print logs
  setLogLevel = function (level) {
    if (level > NONE) {
      level = NONE;
    } else if (level < DEBUG) {
      level = DEBUG;
    }
    logLevel = level;
  };

  // Generic function to print logs for a given level: [DEBUG, TRACE, INFO, WARNING, ERROR]
  log = function () {
    var level = arguments[0];
    var args = arguments;
    if (level < logLevel) {
      return;
    }
    switch (level) {
    case DEBUG:
      args[0] = getTimestamp() + ' DEBUG:';
      console.log.apply(console, args);
      break;
    case INFO:
      args[0] = getTimestamp() + ' INFO:';
      console.log.apply(console, args);
      break;
    case WARNING:
      args[0] = getTimestamp() + ' WARN:';
      console.warn.apply(console, args);
      break;
    case ERROR:
      args[0] = getTimestamp() + ' ERROR:';
      console.error.apply(console, args);
      break;
    default:
      args[0] = getTimestamp() + ' DEFAULT:';
      console.log.apply(console, args);
      return;
    }
  };

  // It prints debug logs
  debug = function () {
    var args = [DEBUG];
    for (var i = 0; i < arguments.length; i++) {
      args.push(arguments[i]);
    }
    log.apply(this, args);
  };

  // It prints info logs
  info = function () {
    var args = [INFO];
    for (var i = 0; i < arguments.length; i++) {
      args.push(arguments[i]);
    }
    log.apply(this, args);
  };

  // It prints warning logs
  warning = function () {
    var args = [WARNING];
    for (var i = 0; i < arguments.length; i++) {
      args.push(arguments[i]);
    }
    log.apply(this, args);
  };

  // It prints error logs
  error = function () {
    var args = [ERROR];
    for (var i = 0; i < arguments.length; i++) {
      args.push(arguments[i]);
    }
    log.apply(this, args);
  };

  return {
    DEBUG: DEBUG,
    INFO: INFO,
    WARNING: WARNING,
    ERROR: ERROR,
    NONE: NONE,
    setLogLevel: setLogLevel,
    log: log,
    debug: debug,
    info: info,
    warning: warning,
    error: error
  };
}());

export default Logger


