import {EventDispatcher, MediaEvent} from './Events'
import AgoraCall from './utils/AgoraCall'
import Logger from "./utils/Logger"
import { report } from './Report2';

var Signal = function (spec) {
  var that = EventDispatcher(spec);
  that.needReconnect = true;
  that.isTimeout = false;
  that.isInit = true;

  // test
  // spec.host = [
  //   "webgw-shaoxing-cucc-1111.agora.io:5867",
  //   "gw.agora.io:5688",
  //   "webgw-shaoxing-cucc-1.agora.io:5867"
  // ];

  that.hostIndex = 0;
  that.requestID = 0;
  if(spec.host instanceof Array) {
    that.host = spec.host;
  } else {
    that.host = [spec.host];
  }

  that.Reconnection = function(host) {
    that.host = host || that.host;
    that.hostIndex = 0;
    that.creatConnection();
  }

  that.getURL = function(){
    return that.connection.url;
  }

  that.creatConnection = function() {
    if(that.hostIndex >= that.host.length) {
      that.hostIndex = 0;
      that.dispatchEvent(MediaEvent({type: "reconnect"}));
      return; 
    }
    Logger.debug("start connect:" +that.host[that.hostIndex]);
    that.lts =  new Date().getTime();

    //agora daily build
    // that.connection = new WebSocket("wss://" + that.host[that.hostIndex++]);
    that.connection = new WebSocket("wss://webgw-test-5.agora.io:5866");that.hostIndex++;

    //onopen
    that.connection.onopen = function (e) {
      Logger.debug("websockect opened");
      that.isTimeout = false;
      that.isInit = false;
      clearTimeout(that.timeoutCheck);
      that.dispatchEvent(MediaEvent({type: 'onopen', event: e, socket: that}));
    };

    //onmessage
    that.connection.onmessage = function (e) {
      // Logger.debug("websockect onmessage:", e);
      var m = JSON.parse(e.data);
      if (m.hasOwnProperty("_id")) {
        that.dispatchEvent(MediaEvent({type: m._id, msg: m}));
      } else if (m.hasOwnProperty("_type")) {
        that.dispatchSocketEvent(MediaEvent({type: m._type, msg: m.message}));
      }
    };

    //onclose
    that.connection.onclose = function (e) { 
      if (!that.needReconnect) {
          Logger.debug("websockect closeed");
          AgoraCall(spec.onFailure, e);
          clearTimeout(that.timeoutCheck);
          that.dispatchEvent(MediaEvent({type: 'close', event: e}));
          that.connection.onopen = undefined;
          that.connection.onclose = undefined;
          that.connection.onerror = undefined;
          that.connection.onmessage = undefined;
          that.connection = undefined;
      } else {
        if(!that.isTimeout && !that.isInit) {
          that.dispatchEvent(MediaEvent({type: 'disconnect', event: e}));
        } else {
          Logger.debug("websockect connect timeout");
          report.joinGateway(spec.sid, {
            lts: that.lts,
            succ: false,
            ec: "timeout",
            addr: that.connection.url,
          });
          that.creatConnection();
        }
      }
    };
  
    //onerror : 
    // it's very weird that invoke socket.close will triger onerror, 
    // and this callback just return "error" but nothing useful details.
    that.connection.onerror = function (e) {
      // if (!that.needReconnect) {
      //   console.log(e);
      //   AgoraCall(spec.onFailure, e);
      //   that.dispatchEvent(MediaEvent({type: 'connect_error', event: e}));
      //   that.connection.onopen = undefined;
      //   that.connection.onclose = undefined;
      //   that.connection.onerror = undefined;
      //   that.connection.onmessage = undefined;
      //   that.connection = undefined;
      // }
    };

    // timeout check
    var timeoutCheck = setTimeout(function() {
      if(that.connection && that.connection.readyState != WebSocket.OPEN) {
        that.isTimeout = true;
        that.connection.close();
      }
    }, 5000); 
  }
  that.creatConnection();
  
  that.sendMessage = function (obj, onError) {
    if (that.connection && that.connection.readyState == WebSocket.OPEN) {
      that.connection.send(JSON.stringify(obj));
    } else {
      // console.log('Connection to gateway lost while sending ' + obj);
      // console.log('signal connection state ' + that.connection.readyState);
      onError({error: 'Gateway not connected'});
    }
  };

  that.disconnect = function () {
    that.needReconnect = true;
    that.connection.close();
  };

  that.close = function () {
    that.needReconnect = false;
    that.connection.close();
  };

  that.sendSignalCommand = function (message, callback) {
      message._id = "_request_" + that.requestID;
      that.requestID += 1;
      if (!(message._type === 'publish_stats' || message._type === 'subscribe_stats' || message._type === 'publish_stats_low')) {
        that.on(message._id, function (e) {
            callback && callback(e.msg._result, e.msg.message);
            delete that.dispatcher.eventListeners[message._id];
        });
      }
      that.sendMessage(message, function (e) {
        e.reason = 'NOT_CONNECTED';
        callback && callback(e.reason, e);
      });
  };

  return that;
};

export default Signal



