import * as config from './Config';
import Logger from "./utils/Logger"
import ErrorCodes from "./utils/ErrorCodes"
import {post, shouldUseHttps} from './utils/Request';
import {report} from "./Report2";
import { CSCrashRecord } from './Helper';

var requestChooseServer = function(url, option, onSuccess, onFailure) {
  var lts = new Date().getTime();

  var csOption = {
    key: option.appId,
    cname: option.cname,
    uid: option.uid
  }

  post(url, csOption, function (res) {
    var response = JSON.parse(res);
    var error = response.error;

    if (error != undefined) {
      report.joinChooseServer(option.sid, {
        lts: lts,
        succ: false,
        csAddr: url,
        serverList: null,
        ec: error,
      })
      onFailure("Get server node failed [" + error + "]", url);

    } else {
      onSuccess(response, url);
    }
  }, function (e, url) {
    if(e.type === "timeout") {
      report.joinChooseServer(option.sid, {
        lts: lts,
        succ: false,
        csAddr: url,
        serverList: null,
        ec: "timeout",
      })
      onFailure("Connect choose server timeout", url);

    } else {
      report.joinChooseServer(option.sid, {
        lts: lts,
        succ: false,
        csAddr: url,
        serverList: null,
        ec: "server_wrong",
      })
      // onFailure("Connect choose server error", url);
    }
  });
}

var requestGatewayList = function(joinInfo, onSuccess, onFailure) {
  var lts = new Date().getTime();
  var connected = false;
  CSCrashRecord.record(lts);

  var success = function(response, url) {
    if(!connected) {
      clearTimeout(st);
      connected = true;
      Logger.debug("Get gateway address:", response.gateway_addr);
      onSuccess(response);

      report.joinChooseServer(joinInfo.sid,{
        lts: lts,
        succ: true,
        csAddr: url,
        serverList: response.gateway_addr,
        cid: response.cid + "",
        uid: response.uid + "",
        ec: null,
      }, true);

    } else {
      report.joinChooseServer(joinInfo.sid,{
        lts: lts,
        succ: true,
        csAddr: url,
        serverList: response.gateway_addr,
        cid: response.cid + "",
        uid: response.uid + "",
        ec: null,
      }, false);
    }
  }

  var failed = function(e, url) {
    Logger.error(e, url);
  }

  var domainlist = config.WEBCS_DOMAIN
  for (var i = 0; i < domainlist.length; ++i) { 
    var csURL;
    csURL = shouldUseHttps() ? 
      "https://" + domainlist[i] + ":5668/getwebgw/jsonp" :
      "http://" + domainlist[i] + ":5669/getwebgw/jsonp";
    // Logger.debug("Connect to backup_choose_server:", csURL);
    requestChooseServer(csURL, joinInfo, success, failed);
  }

  var st = setTimeout(function() {
    if(!connected) {
      var backupList = config.WEBCS_DOMAIN_BACKUP_LIST;
      for (var i = 0; i < backupList.length; ++i) { 
        var csURL;
        csURL = shouldUseHttps() ? 
          "https://" + backupList[i] + ":5668/getwebgw/jsonp" :
          "http://" + backupList[i] + ":5669/getwebgw/jsonp";
        // Logger.debug("Connect to backup_choose_server:", csURL);
        requestChooseServer(csURL, joinInfo, success, failed);
      }
    }
  }, 1000);
  
  setTimeout(function() {
    if(!connected) {
      onFailure();
    }
  }, config.WEBCS_BACKUP_CONNECT_TIMEOUT);
}

//TODO: put it in Controll layer
var getGatewayList = function(param, onSuccess, onFailure) {
  var isSuccess = false;
  var timer = null;
  var times = 1;

  var request = function() {
    // To prevent so many request that make server crash.
    if(CSCrashRecord.shouldBlock()) {
      Logger.warning("CS connetion crash too many times in 10s");
      return onFailure && onFailure();
    }

    if(!isSuccess) {
      requestGatewayList(param, function(gatewayAddr) {
        isSuccess = true;
        clearTimeout(timer);
        onSuccess(gatewayAddr);
      }, function() {
        // Backoff mechanism is reconnecting after double previous time,like 1,2,4,8,16,32... until 3600.
        Logger.debug("Request gateway list will be restart in "+ times +"s");
        timer = setTimeout(function() {
          request();
        }, times * 1000);  
        times = times >= 3600 ? 3600 : times*2;
      });    

    }
  }
  request();
}

export {getGatewayList}


