import * as config from './Config';
import Logger from './utils/Logger'
import {post, shouldUseHttps} from './utils/Request'

const SESSION_INIT       = 'session_init';
const JOIN_CHOOSE_SERVER = 'join_choose_server';
const JOIN_GATEWAY       = 'join_gateway';
const PUBLISH            = 'publish';
const SUBSCRIBE          = 'subscribe';
const FIRST_REMOTE_FRAME = 'first_remote_frame';
const STREAM_SWITCH      = 'stream_switch';
const AUDIO_SENDING_STOPPED = 'audio_sending_stopped';
const VIDEO_SENDING_STOPPED = 'video_sending_stopped';


var base = {
  type: null,
  sid: null,
  lts: null,
  succ: null,
  cname: null,
  uid: null,
  peerid: null,
  cid: null,
  elaps: null,
  extend: null,
  vid: 0,
}

var report = (function() {
    var that = {};
    that.list = {};
    that.url = shouldUseHttps() ? 
    `https://${config.EVENT_REPORT_DOMAIN}:6443/events/report` :
    `http://${config.EVENT_REPORT_DOMAIN}:6080/events/report`;

    that.urlBackup = shouldUseHttps() ? 
    `https://${config.EVENT_REPORT_BACKUP_DOMAIN}:6443/events/report` :
    `http://${config.EVENT_REPORT_BACKUP_DOMAIN}":6080/events/report`;
  
    that.sessionInit = function(sid, param) {
      param.sid = sid;
      var head = Object.assign(base, param);
      // the first time join
      if(!that.list[sid]) {
        head.startTime = +new Date();
      }
      var si = Object.assign({}, head);
      
      delete head.appid;
      delete head.mode;
      that.list[sid] = head;
      
      si.extend = null;
      si.ver = config.VERSION;
      si.type = SESSION_INIT;
      si.browser = navigator.userAgent;
      si.lts = +new Date();
      si.elaps = si.lts - si.startTime;
      that.send(si);
    }
  
    that.joinChooseServer = function(sid, param, coverHead) {
      var jcs;
      // when multi cs request, only the first result will cover the head
      if(coverHead) {
        jcs = Object.assign(that.list[sid], param);  
      } else {
        jcs = Object.assign({}, that.list[sid], param);  
      }
      jcs.type = JOIN_CHOOSE_SERVER;
      var endTime = +new Date();
      jcs.ev_elaps = endTime - jcs.lts;
      jcs.elaps = endTime - jcs.startTime;
      jcs.lts = endTime;
      jcs.serverList = JSON.stringify(jcs.serverList);
      that.send(jcs);
    }
  
    that.joinGateway = function(sid, param) {
      var jg = Object.assign(that.list[sid], param);
      jg.type = JOIN_GATEWAY;
      var endTime = +new Date();
      jg.ev_elaps = endTime - jg.lts;
      jg.elaps = endTime - jg.startTime;
      jg.lts = endTime;
      that.send(jg);
    }
  
    that.publish = function(sid, param) {
      var pu = Object.assign({}, that.list[sid], param);
      pu.type = PUBLISH;
      // We have a previous LTS which is defined as a timestamp of start point. Now a new LTS is created for the end point. And ev_elapse is the difference between peers.
      var endTime = +new Date();
      pu.ev_elaps = endTime - pu.lts;
      pu.elaps = endTime - pu.startTime;
      pu.lts = endTime;
      that.send(pu);
    }
  
    that.subscribe = function(sid, param) {
      var sub = Object.assign({}, that.list[sid], param);
      sub.type = SUBSCRIBE;
      // We have a previous LTS which is defined as a timestamp of start point. Now a new LTS is created for the end point. And ev_elapse is the difference between peers.
      var endTime = +new Date();
      sub.ev_elaps = endTime - sub.lts;
      sub.elaps = endTime - sub.startTime;
      sub.lts = endTime;
      that.send(sub);
    }

    that.firstRemoteFrame = function(sid, param) {
      var frf = Object.assign({}, that.list[sid], param);
      frf.type = FIRST_REMOTE_FRAME;
      // frf.elaps = null;
      frf.lts = +new Date();
      frf.elaps = frf.lts - frf.startTime;
      that.send(frf);
    }

    that.streamSwitch = function(sid, param) {
      var ss = Object.assign({}, that.list[sid], param);
      ss.type = STREAM_SWITCH;
      ss.isdual = param.isdual;
      ss.lts = +new Date();
      ss.elaps = ss.lts - ss.startTime;
      that.send(ss);
    }

    that.audioSendingStopped = function(sid, param) {
      var ass = Object.assign({}, that.list[sid], param);
      ass.type = AUDIO_SENDING_STOPPED;
      ass.lts = +new Date();
      ass.elaps = ass.lts - ass.startTime;
      that.send(ass);
    }

    that.videoSendingStopped = function(sid, param) {
      var vss = Object.assign({}, that.list[sid], param);
      vss.type = VIDEO_SENDING_STOPPED;
      vss.lts = +new Date();
      vss.elaps = vss.lts - vss.startTime;
      that.send(vss);
    }

    that.send = function(data) {
      try {
        post(that.url, data, null, function(e) {
          post(that.urlBackup, data, null, function(e) {
            // user should not see anything about report
            // Logger.warning("Event report failed");
          }, {timeout: 10000});
        }, {timeout: 10000});
      } catch (error) {
        // won't throw out error
      }
    }
    return that;
  })();

export {
  report,
  SESSION_INIT,
  JOIN_CHOOSE_SERVER,
  JOIN_GATEWAY,
  PUBLISH,
  SUBSCRIBE,
}


