import Signal from './Signal'
import {MediaEvent} from './Events'

var Socket = function (host, spec) {

    var that = {};

    that.connect = function() {
        spec.host = host;
        that.signal = Signal(spec);
        that.on = that.signal.on;
        that.dispatchEvent = that.signal.dispatchEvent;

        that.signal.on('onopen', function (e) {
            that.signal.onEvent = function (e) {
                that.dispatchEvent(MediaEvent({type: e.event, msg: e}));
            };

            that.dispatchEvent(MediaEvent({type: 'connect', msg: e}));
        });

        that.signal.on('onError', function (ev) {
            var m = ev.msg;
            onError(m.code, 'error');
        });
    };

    that.disconnect = function() {
        that.signal.disconnect();
    };

    that.close = function() {
        that.signal.close();
    };

    that.getURL = function() {
        return that.signal.getURL();
    };

    that.reconnect = function(host) {
        that.signal.creatConnection(host);
    };

    that.emitSimpleMessage = function(message, callback) {
        that.signal.sendSignalCommand(message, callback);
    };

    that.connect();
    return that;
};

export default Socket



