/*
 * View class represents a HTML component
 * Every view is an EventDispatcher.
 */

import {EventDispatcher} from '../Events'

var View = function (spec) {
  "use strict";

  var that = EventDispatcher(spec);

  // Variables

  // URL where it will look for icons and assets
  that.url = ".";
  return that;
};

export default View



