import {Stream} from "./Stream"
import Socket from "./Websocket"
import {Connection} from "./Connection"
import {EventDispatcher, StreamEvent, ClientEvent, LiveStreamingEvent} from "./Events"
import Logger from "./utils/Logger"
import ErrorCodes from "./utils/ErrorCodes"
import AgoraCall from "./utils/AgoraCall"
import {getGatewayList} from "./Server"
import {VERSION} from "./Config"
import {report} from "./Report2"
import {vsResHack} from "./Helper"
import { isFireFox } from "./utils/Browser";

var GatewayClient = function (spec) {
    "use strict";

    var isBanned = false;
    var Ping = function () {
        return {"_type":"ping"};
    };
    var Join = function() {
        var join = {
          "key": that.key,
          "channel":that.joinInfo.cname,
          "uid": that.uid,
          "version": VERSION,
          "browser": navigator.userAgent,
          "mode": spec.mode,
          "role": spec.role,
          "config": that.config
        };
        return {"_type":"join1","message":join};
    };
    var Leave = function () {
      return {"_type":"leave"};
    };
    var Control = function (options) {
      return {"_type":"control","message":options};
    };
    var Token = function (spec) {
      // "spec":{"mode":"live","version":"1.12.0","host":"webgw-washington-na-1.agora.io:5866"}
      return {"_type":"token","message":spec};
    };
    var P2PLost = function () {
      return {"_type":"p2p_lost","message":null};
    };
    var Unpublish = function (streamId) {
      return {"_type":"unpublish","message":streamId};
    };
    var Unsubscribe = function (streamId) {
      return {"_type":"unsubscribe","message":streamId};
    };
    var SwitchVideoStream = function (streamId, streamType) {
      return {"_type":"switchVideoStream","message":{id: streamId, type: streamType}};
    };
    var Publish = function (options, sdp) {
      return {"_type":"publish","options":options,"sdp":sdp};
    };
    var PublishStats = function (stats) {
      return {"_type":"publish_stats","options":{"stats":stats},"sdp":null};
    };
    var PublishStatsLow = function (stats) {
      return {"_type":"publish_stats_low","options":{"stats":stats},"sdp":null};
    };
    var Subscribe = function (options, sdp) {
      return {"_type":"subscribe","options":options,"sdp":sdp};
    };
    var SubscribeStats = function (streamId, stats) {
      return {"_type":"subscribe_stats","options":{id: streamId, stats: stats},"sdp":null};
    };
    var StartLiveStreaming = function (url, transcodingEnabled) {
      return {"_type": "start_live_streaming", "message": {url: url, transcodingEnabled: transcodingEnabled}};
    };
    var StopLiveStreaming = function (url) {
      return {"_type": "stop_live_streaming", "message": {url: url}};
    };
    var SetLiveTranscoding = function (transcoding) {
      return {"_type": "set_live_transcoding", "message": {transcoding: transcoding}};
    };

    var DISCONNECTED = 0;
    var CONNECTING = 1;
    var CONNECTED = 2;

    var that = EventDispatcher(spec);
    that.socket = undefined;
    that.state = DISCONNECTED;
    that.mode = spec.mode;
    that.role = spec.role;
    that.codec = spec.codec;
    that.config = {};
    that.timers = {};
    that.timer_counter = {};
    that.localStreams = {};
    that.remoteStreams = {};
    that.attemps = 1;
    that.p2p_attemps = 1;
    that.audioLevel = {};
    that.activeSpeaker = undefined;
    that.firstFrameTimer = new Map();

    that.liveStreams = new Map();

    var safeCall = AgoraCall;

    that.remoteVideoStreamTypes = {
        REMOTE_VIDEO_STREAM_HIGH: 0,
        REMOTE_VIDEO_STREAM_LOW: 1,
        REMOTE_VIDEO_STREAM_MEDIUM: 2
    };

    that.configPublisher = function (config) {
      that.config = config;
    };

    that.join = function(joinInfo, channelKey, onSuccess, onFailure) {
        var lts = new Date().getTime();
        var uid = joinInfo.uid;

        if (that.state !== DISCONNECTED) {
          onFailure && onFailure(ErrorCodes.INVALID_OPERATION);
          report.joinGateway(joinInfo.sid, {
            lts: lts,
            succ: false,
            ec: ErrorCodes.INVALID_OPERATION,
            addr: null,
          });
          Logger.error("Server already in connecting/connected state");
          return;
        }

        if (uid !== null && uid !== undefined && parseInt(uid) !== uid) { //user specified uid MUST be integer
          onFailure && onFailure(ErrorCodes.INVALID_PARAMETER);
          report.joinGateway(joinInfo.sid, {
            lts: lts,
            succ: false,
            ec: ErrorCodes.INVALID_PARAMETER,
            addr: null,
          });
          Logger.error("Input uid is invalid");
          return;
        }

        that.joinInfo = joinInfo;
        that.uid = uid;
        that.key = channelKey;
        // Connect to VosGateway
        that.state = CONNECTING;
        connectSocket(
          joinInfo
          , function (response) {
            that.stunServerUrl = response.stunServerUrl;
            that.turnServer = response.turnServer;
            that.state = CONNECTED;
            Logger.debug("Connected to gateway server");

            that.pingTimer = setInterval(
              function() {
                  sendMessage(
                    Ping()
                    , function () {}
                    , function (e) {}
                  );
              }
              , 3000);

            sendMessage(
              Join()
              , function (message) {
                report.joinGateway(joinInfo.sid, {
                  lts: lts,
                  succ: true,
                  ec: null,
                  vid: message.vid,
                  addr: that.socket.getURL(),
                })
                onSuccess(that.uid);
              }
              , function (err) {
                Logger.error("User join failed [" + err + "]");
                if (err === ErrorCodes.ERR_INVALID_VENDOR_KEY) {
                  err = ErrorCodes.INVALID_KEY;
                } else if (err === ErrorCodes.ERR_INVALID_DYNAMIC_KEY) {
                  err = ErrorCodes.INVALID_DYNAMIC_KEY;
                } else if (err === ErrorCodes.ERR_DYNAMIC_KEY_TIMEOUT) {
                  err = ErrorCodes.DYNAMIC_KEY_TIMEOUT;
                } else if (err === ErrorCodes.ERR_NO_VOCS_AVAILABLE) {
                  err = ErrorCodes.NO_VOCS_AVAILABLE;
                } else if (err === ErrorCodes.ERR_NO_VOS_AVAILABLE) {
                  err = ErrorCodes.NO_VOS_AVAILABLE;
                } else if (err === ErrorCodes.ERR_JOIN_CHANNEL_TIMEOUT) {
                  err = ErrorCodes.JOIN_CHANNEL_TIMEOUT;
                } else if (err === ErrorCodes.ERR_FAILED) {
                  err = ErrorCodes.FAILED;
                } else if (err === ErrorCodes.WARN_NO_AVAILABLE_CHANNEL) {
                  err = ErrorCodes.NO_AVAILABLE_CHANNEL;
                } else if (err === ErrorCodes.WARN_LOOKUP_CHANNEL_TIMEOUT) {
                  err = ErrorCodes.LOOKUP_CHANNEL_TIMEOUT;
                } else if (err === ErrorCodes.WARN_LOOKUP_CHANNEL_REJECTED) {
                  err = ErrorCodes.LOOKUP_CHANNEL_REJECTED;
                } else if (err === ErrorCodes.WARN_OPEN_CHANNEL_TIMEOUT) {
                  err = ErrorCodes.OPEN_CHANNEL_TIMEOUT;
                } else if (err === ErrorCodes.WARN_OPEN_CHANNEL_REJECTED) {
                  err = ErrorCodes.OPEN_CHANNEL_REJECTED;
                } else if (err === ErrorCodes.WARN_REQUEST_DEFERRED) {
                  err = ErrorCodes.REQUEST_DEFERRED;
                }

                onFailure && onFailure(err);
                report.joinGateway(joinInfo.sid, {
                  lts: lts,
                  succ: false,
                  ec: err,
                  addr: that.socket.getURL(),
                });
              });
            }
          , function (error) {
            error = JSON.stringify(error);
            Logger.error("User join failed [" + error + "]");
            onFailure && onFailure(error);
            report.joinGateway(joinInfo.sid, {
              lts: lts,
              succ: false,
              ec: error,
              addr: that.socket.getURL(),
            });
        });
    };

  that.leave = function(onSuccess, onFailure) {
    // Logger.debug("Leaving channel ts", new Date().getTime());
    if (that.state != CONNECTED) {
      safeCall(onSuccess);
      return ;
    }

    clearInterval(that.pingTimer);
    for (var i in that.timers) {
      if (that.timers.hasOwnProperty(i)) {
        clearInterval(that.timers[i]);
      }
    }

    sendMessage(Leave(), function (message) {
      that.socket.close();
      that.socket = undefined;
      Logger.info("Leave channal success");
      onSuccess && onSuccess(message);
    }, onFailure);

    // Remove local streams
    for (var i in that.localStreams) {
      if (that.localStreams.hasOwnProperty(i)) {
        var stream = that.localStreams[i];
        delete that.localStreams[i];
        if (stream.pc !== undefined) {
          stream.pc.close();
          stream.pc = undefined;
        }
      }
    }

    removeRemoteStreams();

    that.state = DISCONNECTED;
  };
  
  // It publishes the stream provided as argument.
  that.publish = function (stream, onSuccess, onFailure) {
    var lts = new Date().getTime();
    //TODO rwrite - for the dtls failed can not raise event to report
    stream.publishLTS = lts;

    if (typeof stream !== "object" || stream === null) {
      Logger.error("Invalid local stream");
      onFailure && onFailure(ErrorCodes.INVALID_LOCAL_STREAM);
      report.publish(that.joinInfo.sid, {
        lts: lts,
        succ: false,
        ec: ErrorCodes.INVALID_LOCAL_STREAM,
      });
      return;
    }
    if (stream.stream === null && stream.url === undefined) {
      Logger.error("Invalid local media stream");
      onFailure && onFailure(ErrorCodes.INVALID_LOCAL_STREAM);
      report.publish(that.joinInfo.sid, {
        lts: lts,
        succ: false,
        ec: ErrorCodes.INVALID_LOCAL_STREAM,
      });
      return;
    }
    if (that.state !== CONNECTED) {
      Logger.error("User is not in the session");
      onFailure && onFailure(ErrorCodes.INVALID_OPERATION);
      report.publish(that.joinInfo.sid, {
        lts: lts,
        succ: false,
        ec: ErrorCodes.INVALID_OPERATION,
      });
      return;
    }
    var options = stream.getAttributes() || {};
    // If the stream is not local we do nothing
    if (stream.local && that.localStreams[stream.getId()] === undefined) {
      // Publish media stream to VoS gateway
      if (stream.hasAudio() || stream.hasVideo() || stream.hasScreen()) {
        if (stream.url !== undefined) {
          // TODO:   --
          sendSimpleSdp(
            Publish({
              state: "url", 
              audio: stream.hasAudio(), 
              video: stream.hasVideo(), 
              attributes: stream.getAttributes(), 
              mode: that.mode
            }, stream.url), 
            function (answer, id) {
              if (answer === "success") {
                stream.getUserId = function () {
                  return id;
                };
                that.localStreams[id] = stream;
                stream.onClose = function () {
                  that.unpublish(stream);
                };
              } else {
                Logger.error("Publish local stream failed", answer);
              }
            }
          );
        } else {
          // Keep record of the local stream before sending publish message.
          // We rely on this record to recover when reconnecting.
          that.localStreams[stream.getId()] = stream;
          stream.pc = Connection({
            callback: function (offer) {
              Logger.debug("SDP exchange in publish : send offer --  ", JSON.parse(offer));
              sendSimpleSdp(
                Publish({
                  state: "offer", 
                  id: stream.getId(), 
                  audio: stream.hasAudio(), 
                  video: stream.hasVideo() || stream.hasScreen(), 
                  attributes: stream.getAttributes(), 
                  mode: that.mode, codec: that.codec
                }, offer), 
                function (answer, info) {
                  if (answer === "error") {
                    Logger.error("Publish local stream failed");
                    onFailure && onFailure(ErrorCodes.PUBLISH_STREAM_FAILED);
                    report.publish(that.joinInfo.sid, {
                      lts: lts,
                      succ: false,
                      localSDP: offer,
                      ec: ErrorCodes.PUBLISH_STREAM_FAILED,
                    });
                    return;
                  }

                  stream.pc.onsignalingmessage = function (ok) {
                    stream.pc.onsignalingmessage = function () {};
                    sendSimpleSdp(
                      Publish({
                        state: "ok", 
                        id: stream.getId(), 
                        audio: stream.hasAudio(), 
                        video: stream.hasVideo(), 
                        screen: stream.hasScreen(), 
                        attributes: stream.getAttributes(), 
                        mode: that.mode
                      }, ok)
                    );

                    stream.getUserId = function () {
                      return info.id;
                    }
                    Logger.info("Local stream published with uid", info.id);
                    stream.onClose = function () {
                      that.unpublish(stream);
                    }
                    stream.unmuteAudio = function() {
                      sendMessage(Control({"action": "audio-out-on", "streamId": stream.getId()}), function(){}, function(){});
                    };
                    stream.unmuteVideo = function() {
                      sendMessage(Control({"action": "video-out-on", "streamId": stream.getId()}), function(){}, function(){});
                    };
                    stream.muteAudio = function() {
                      sendMessage(Control({"action": "audio-out-off", "streamId": stream.getId()}), function(){}, function(){});
                    };
                    stream.muteVideo = function() {
                      sendMessage(Control({"action": "video-out-off", "streamId": stream.getId()}), function(){}, function(){});
                    };
                  };

                  stream.pc.oniceconnectionstatechange = function (state) {
                    if (state === "failed") {
                      if (that.timers[stream.getId()] != undefined) {
                        clearInterval(that.timers[stream.getId()]);
                      }
                      sendMessage(P2PLost(), function () {}, function () {});
                      that.socket.disconnect();
                      Logger.error("Publisher connection is lost");
                      onFailure && onFailure(ErrorCodes.PEERCONNECTION_FAILED);
                      report.publish(that.joinInfo.sid, {
                        lts: lts,
                        succ: false,
                        ec: ErrorCodes.PEERCONNECTION_FAILED,
                      });

                    } else if (state === "connected") {
                      onSuccess && onSuccess();
                      // Logger.info("Publish success, uid:", stream.getId());
                      report.publish(that.joinInfo.sid, {
                        lts: lts,
                        succ: true,
                        ec: null,
                      });
                    }
                  };

                  Logger.debug("SDP exchange in publish : receive answer --  ", JSON.parse(answer));
                  stream.pc.processSignalingMessage(answer);
                });
            }, audio: stream.hasAudio(), 
            video: stream.hasVideo(), 
            screen: stream.hasScreen(), 
            isSubscriber: false, 
            iceServers: [], 
            stunServerUrl: that.stunServerUrl,
            turnServer: that.turnServer, 
            maxAudioBW: options.maxAudioBW, 
            minVideoBW: options.minVideoBW, 
            maxVideoBW: options.maxVideoBW, 
            mode:that.mode, 
            codec:that.codec
          });

          stream.pc.addStream(stream.stream);
          Logger.debug("PeerConnection add stream :", stream.stream);
          that.timers[stream.getId()] = setInterval(function() {
            if (stream && stream.pc && stream.pc.getStats) {
              stream.pc.getStatsRate(function(stats) {
                stats.forEach(function(stat) {
                  // Firefox and Safari get resolution from stream
                  if ((stat.id.indexOf("outbound_rtp") !== -1 ||
                    stat.id.indexOf("OutboundRTP") !== -1) && 
                    stat.mediaType === "video") {
                      stat.googFrameWidthSent = stream.videoWidth + '';
                      stat.googFrameHeightSent = stream.videoHeight + '';
                  }

                  // stream.getUserId wiil be undefined if pc dtls failed
                  if(!stream.getUserId) return;
                  
                  if(stream.getId() === stream.getUserId()) {
                    sendMessage(PublishStats(stat), null, null);
                  } else {
                    sendMessage(PublishStatsLow(stat), null, null);
                  }
                  
                });
              });
            }
          }, 3000);

          if (stream.aux_stream !== undefined) {
            stream.pc.addStream(stream.aux_stream);
            Logger.debug("PeerConnection add stream :", stream.aux_stream);
          }
        }
      }
    }
  };

  // It unpublishes the local stream,
  // dispatching a StreamEvent("stream-removed")
  that.unpublish = function (stream, onSuccess, onFailure) {
    // Logger.debug("Start unpublishing local stream");
    if (typeof stream !== "object" || stream === null) {
      safeCall(onFailure, ErrorCodes.INVALID_LOCAL_STREAM);
      Logger.error("Invalid local stream");
      return;
    }
    if (that.state !== CONNECTED) {
      safeCall(onFailure, ErrorCodes.INVALID_OPERATION);
      Logger.error("User not in the session");
      return;
    }
    if (that.timers[stream.getId()] != undefined) {
      clearInterval(that.timers[stream.getId()]);
    }
    if (that.socket !== undefined) {
      // Unpublish stream from VoS gateway
      if (stream.local && that.localStreams[stream.getId()] !== undefined) {
        delete that.localStreams[stream.getId()];
        sendMessage(Unpublish(stream.getUserId()), function (answer) {
          if (answer === "error") {
            safeCall(onFailure, ErrorCodes.UNPUBLISH_STREAM_FAILED);
            Logger.error("Unpublish stream failed");
            return;
          }
          if ((stream.hasAudio() || stream.hasVideo() || stream.hasScreen()) && stream.url === undefined) {
            if (stream.pc !== undefined) {
              stream.pc.close();
              stream.pc = undefined;
            }
          }
          stream.onClose = undefined;
          stream.unmuteAudio = undefined;
          stream.muteAudio = undefined;
          stream.unmuteVideo = undefined;
          stream.muteVideo = undefined;
          onSuccess && onSuccess();
          // Logger.info("Unpublish stream success, uid:", stream.getId());
        }, onFailure);
      } else {
        safeCall(onFailure, ErrorCodes.INVALID_LOCAL_STREAM);
        Logger.error("Invalid local stream");
      }
    } else {
      safeCall(onFailure, ErrorCodes.INVALID_OPERATION);
      Logger.error("User not in the session");
    }
  };

  // It subscribes to a remote stream and draws it inside the HTML tag given
  // by the ID="elementID"
  that.subscribe = function (stream, onFailure) {
    var lts = new Date().getTime();
    //TODO rwrite - for the dtls failed can not raise event to report
    stream.subscribeLTS = lts;

    if (typeof stream !== "object" || stream === null) {
      onFailure && onFailure(ErrorCodes.INVALID_REMOTE_STREAM);
      report.subscribe(that.joinInfo.sid, {
        lts: lts,
        succ: false,
        peerid: stream.getId() + "",
        ec: ErrorCodes.INVALID_REMOTE_STREAM,
      });
      Logger.error("Invalid remote stream");
      return;
    }
    if (that.state !== CONNECTED) {
      onFailure && onFailure(ErrorCodes.INVALID_OPERATION);
      report.subscribe(that.joinInfo.sid, {
        lts: lts,
        succ: false,
        peerid: stream.getId() + "",
        ec: ErrorCodes.INVALID_OPERATION,
      });
      Logger.error("User is not in the session");
      return;
    }
    if (!stream.local && that.remoteStreams.hasOwnProperty(stream.getId())) {
      if (stream.hasAudio() || stream.hasVideo() || stream.hasScreen()) {
        stream.pc = Connection({
          callback: function (offer) {
            Logger.debug("SDP exchange in subscribe : send offer --  ", JSON.parse(offer) );
            sendSimpleSdp(Subscribe({
              streamId: stream.getId(), 
              audio: true, video: true, 
              mode: that.mode, 
              codec: that.codec
            }, offer), 
            function (answer) {
              if (answer === "error") {
                Logger.error("Subscribe remote stream failed, closing stream ", stream.getId())
                stream.close();
                return;
              }
              Logger.debug("SDP exchange in subscribe : receive answer --  ", JSON.parse(answer));
              stream.pc.processSignalingMessage(answer);
            });
          }, 
          nop2p: true, 
          audio: true, 
          video: true, 
          screen: stream.hasScreen(), 
          isSubscriber: true,
          iceServers: [], 
          stunServerUrl: that.stunServerUrl, 
          turnServer: that.turnServer
        });

        stream.pc.onaddstream = function (evt, type) {
          // Draw on html
          if ((type === "ontrack" && evt.track.kind === "video") || type === "onaddstream") {
            Logger.info("Remote stream subscribed with uid " , stream.getId());
            if (type === "onaddstream") {
              that.remoteStreams[stream.getId()].stream = evt.stream;
            } else {
              that.remoteStreams[stream.getId()].stream = evt.streams[0];
            }
            if (!that.remoteStreams[stream.getId()].hasVideo()) {
              that.remoteStreams[stream.getId()].disableVideo();
            } else {
              if(isFireFox()) {
                let st = that.remoteStreams[stream.getId()].stream;
                vsResHack(st, (videoWidth, videoHeight) => {
                  stream.videoWidth = videoWidth;
                  stream.videoHeight = videoHeight;
                }, e => Logger.warning('vsResHack failed:' + e));
              }
            }
            var subscribeEvt = StreamEvent({
              type: "stream-subscribed", stream: that.remoteStreams[stream.getId()]});
            that.dispatchEvent(subscribeEvt);
          }
          stream.unmuteAudio = function () {
            sendMessage(Control({"action": "audio-in-on", "streamId": stream.getId()}), function(){}, function(){});
          };
          stream.muteAudio = function () {
            sendMessage(Control({"action": "audio-in-off", "streamId": stream.getId()}), function(){}, function(){});
          };
          stream.unmuteVideo = function () {
            sendMessage(Control({"action": "video-in-on", "streamId": stream.getId()}), function(){}, function(){});
          };
          stream.muteVideo = function () {
            sendMessage(Control({"action": "video-in-off", "streamId": stream.getId()}), function(){}, function(){});
          };
        };

        // for argus
        that.timers[stream.getId()] = setInterval(function() {
          if (stream && stream.pc && stream.pc.getStats) {
            stream.pc.getStatsRate(function(stats) {
              stats.forEach(function(stat) {
                // Firefox get resolution from stream
                if(stat.id.indexOf("inbound_rtp") !== -1 &&
                  stat.mediaType === "video" ) {
                    stat.googFrameWidthReceived = stream.videoWidth + '';
                    stat.googFrameHeightReceived = stream.videoHeight + '';
                }
                sendSimpleSdp(SubscribeStats(stream.getId(), stat), null, null);
              })
            });
          }
        }, 3000);
        
        // find active-speaker
        that.audioLevel[stream.getId()] = 0;
        that.timers[stream.getId()+"audio"] = setInterval(function() {
          if (stream && stream.pc && stream.pc.getStats) {
            stream.pc.getStats(function(stats) {
              stats.forEach(function(stat) {
                if (stat.mediaType === "audio") {
                  //Logger.info("[zzz] uid:"+stream.getId()+" output level"+stat.audioOutputLevel);
                  if (stat.audioOutputLevel > 5000) {
                    if (that.audioLevel[stream.getId()] < 20) {
                      that.audioLevel[stream.getId()] += 1;
                      //Logger.info("[xxx] user:"+stream.getId()+" counter:"+that.audioLevel[stream.getId()]);
                    }
                    for (var i in that.audioLevel) {
                      if (parseInt(i) !== stream.getId() && that.audioLevel[i] > 0) {
                        that.audioLevel[i] -= 1;
                        //Logger.info("[yyy] user:"+i+" counter:"+that.audioLevel[i]+" stream:"+stream.getId());
                      }
                    }
                  }
                  var unsortedKeys = Object.keys(that.audioLevel);
                  var sortedUids = unsortedKeys.sort(function(uid1, uid2) {
                    return that.audioLevel[uid2] - that.audioLevel[uid1];
                  });
                  if (that.activeSpeaker !== sortedUids[0]) {
                    var evt = ClientEvent({type: "active-speaker", uid: sortedUids[0]});
                    that.dispatchEvent(evt);
                    that.activeSpeaker = sortedUids[0];
                    Logger.debug("Update active speaker:" + that.activeSpeaker);
                  }
                }
              });
            });
          }
        }, 50);
        
        stream.pc.oniceconnectionstatechange = function (state) {
          if (state === "failed") {
            if (that.timers[stream.getId()] != undefined) {
              clearInterval(that.timers[stream.getId()]);
              clearInterval(that.timers[stream.getId()]+"audio");
            }
            sendMessage(P2PLost(), function () {}, function () {});
            that.socket.disconnect();
            Logger.error("Subscriber connection is lost", stream.getId());
            onFailure && onFailure(ErrorCodes.PEERCONNECTION_FAILED);
            report.subscribe(that.joinInfo.sid, {
              lts: lts,
              succ: false,
              peerid: stream.getId() + "",
              ec: ErrorCodes.PEERCONNECTION_FAILED,
            });
            
          } else if (state === "connected") {
            report.subscribe(that.joinInfo.sid, {
              lts: lts,
              succ: true,
              peerid: stream.getId() + "",
              ec: null,
            });
            // Report first frame
            that.firstFrameTimer.set(stream.getId(), setInterval(function() {
              if(stream.pc) {
                stream.pc.getStats(function(stats) {
                  stats.forEach(function(stat) {
                    if(stat.id.indexOf("recv") !== -1 || 
                    stat.id.indexOf("inbound_rtp") !== -1 ||
                    stat.id.indexOf("InboundRTP") !== -1) {
                      if(stat.mediaType === "video") {
                        if(stat.framesDecoded > 0 || stat.googFramesDecoded > 0) {
                          clearInterval(that.firstFrameTimer.get(stream.getId()));
                          that.firstFrameTimer.delete(stream.getId());
                          report.firstRemoteFrame(that.joinInfo.sid, {
                            lts: new Date().getTime(),
                            peerid: stream.getId() + "",
                            succ: true,
                            width: +stat.googFrameWidthReceived,
                            height: +stat.googFrameHeightReceived
                          });
                        }
                      }
                    }
                  })
                });
              } else {
                clearInterval(that.firstFrameTimer.get(stream.getId()));
                that.firstFrameTimer.delete(stream.getId());
              }
            }, 100));

          }
        };
      } else {
        onFailure && onFailure(ErrorCodes.INVALID_REMOTE_STREAM);
        report.subscribe(that.joinInfo.sid, {
          lts: lts,
          succ: false,
          peerid: stream.getId() + "",
          ec: ErrorCodes.INVALID_REMOTE_STREAM,
        });
        Logger.error("Invalid remote stream");
      }
    } else {
      onFailure && onFailure(ErrorCodes.INVALID_REMOTE_STREAM);
      report.subscribe(that.joinInfo.sid, {
        lts: lts,
        succ: false,
        peerid: stream.getId() + "",
        ec: ErrorCodes.INVALID_REMOTE_STREAM,
      });
      Logger.error("Invalid remote stream");
    }
  };

  // It unsubscribes from the stream, removing the HTML element
  that.unsubscribe = function (stream, onFailure) {
    // Logger.debug("Start unsubscribing remote stream with uid", stream.getId());
    if (typeof stream !== "object" || stream === null) {
      safeCall(onFailure, ErrorCodes.INVALID_REMOTE_STREAM);
      Logger.error("Invalid remote stream");
      return;
    }

    if (that.state !== CONNECTED) {
      safeCall(onFailure, ErrorCodes.INVALID_OPERATION);
      Logger.error("User is not in the session");
      return;
    }

    if (that.timers[stream.getId()] != undefined) {
        clearInterval(that.timers[stream.getId()]);
        clearInterval(that.timers[stream.getId()] + "audio");
    }

    if (that.audioLevel[stream.getId()] != undefined) {
      delete(that.audioLevel[stream.getId()]);
    }

    if (that.timer_counter[stream.getId()] != undefined) {
      delete(that.timer_counter[stream.getId()]);
    }

    if (! that.remoteStreams.hasOwnProperty(stream.getId())) {
      safeCall(onFailure, ErrorCodes.NO_SUCH_REMOTE_STREAM);
      return;
    }
    if (!that.socket) {
      safeCall(onFailure, ErrorCodes.INVALID_OPERATION);
      Logger.error("User is not in the session");
      return;
    }

    if (stream.local) {
      safeCall(onFailure, ErrorCodes.INVALID_REMOTE_STREAM);
      Logger.error("Invalid remote stream");
      return ;
    }

    stream.close();
    delete that.remoteStreams[stream.getId()];

    sendMessage(Unsubscribe(stream.getId()), function (answer) {
      if (answer === "error") {
        safeCall(onFailure, ErrorCodes.UNSUBSCRIBE_STREAM_FAILED);
        Logger.error("Unsubscribe remote stream failed", stream.getId());
        return;
      }
      Logger.info("Unsubscrib stream success");
    }, onFailure);
  };

  that.setRemoteVideoStreamType = function (stream, streamType) {
    Logger.debug("Switching remote video stream " + stream.getId() + " to " + streamType);
    if (typeof stream !== "object" || stream === null) {
      Logger.error("Invalid remote stream");
      return;
    }
    if (that.state !== CONNECTED) {
      Logger.error("User is not in the session");
      return;
    }
    if (!stream.local) {
      switch (streamType) {
      case that.remoteVideoStreamTypes.REMOTE_VIDEO_STREAM_HIGH:
      case that.remoteVideoStreamTypes.REMOTE_VIDEO_STREAM_LOW:
      case that.remoteVideoStreamTypes.REMOTE_VIDEO_STREAM_MEDIUM:
        break;
      default:
        //safeCall(onFailure, ErrorCodes.INVALID_OPERATION);
        return;
      }
      sendMessage(SwitchVideoStream(stream.getId(), streamType), null, null);
    } else {
      //safeCall(onFailure, ErrorCodes.INVALID_REMOTE_STREAM);
    }
  };

  that.startLiveStreaming = function (url, transcodingEnabled) {
    that.liveStreams.set(url, transcodingEnabled);
    Logger.debug("Start live streaming " + url + " transcodingEnabled " + transcodingEnabled);
    if (that.state !== CONNECTED) {
      Logger.error("User is not in the session");
      return;
    }
    sendMessage(StartLiveStreaming(url, transcodingEnabled), null, null);
  };

  that.stopLiveStreaming = function (url) {
    Logger.debug("Stop live streaming " + url);
    if (that.state !== CONNECTED) {
      Logger.error("User is not in the session");
      return;
    }
    that.liveStreams.delete(url);
    sendMessage(StopLiveStreaming(url), null, null);
  };

  that.setLiveTranscoding = function (transcoding) {
    that.transcoding = transcoding;
    Logger.debug("Set live transcoding " + transcoding);
    if (that.state !== CONNECTED) {
      Logger.error("User is not in the session");
      return;
    }
    sendMessage(SetLiveTranscoding(transcoding), null, null);
  };

  var generateInterval = function(k) {
    return Math.min(30, (Math.pow(2, k) - 1)) * 1000;
  };

  var recover = function() {
    if (that.key) {
      Logger.info("Re-joining to channel " + that.joinInfo.cname);
      that.join(that.joinInfo, that.key, function (uid) {
        Logger.info("User " + uid + " is re-joined to " + that.joinInfo.cname);
        //TODO rewrite
        for (var streamId in that.localStreams) {
          if (that.localStreams[streamId] !== undefined) {
            var stream = that.localStreams[streamId];
            delete that.localStreams[streamId];
            if (stream.pc !== undefined) {
              stream.pc.close();
              stream.pc = undefined;
            }
          }
        }
        that.dispatchEvent(ClientEvent({type: "rejoin"}));
        // Logger.info("Publish the old local stream again");
        // that.publish(stream, function (err) {
        //   Logger.error("Publish the old stream failed");
        // });
        if(that.liveStreams && that.liveStreams.size) {
          that.liveStreams.forEach((value, key) => {
            if(value) {
              that.setLiveTranscoding(that.transcoding);
            }
            that.startLiveStreaming(key, value);
          });
        }

      }, function (err) {
        Logger.error("Re-join to channel failed [" + err + "]");
        if (err === ErrorCodes.ERR_INVALID_VENDOR_KEY) {
          var evt = StreamEvent({type: "error", reason: ErrorCodes.INVALID_KEY});
          that.dispatchEvent(evt);
        } else if (err === ErrorCodes.ERR_INVALID_DYNAMIC_KEY) {
          var evt = StreamEvent({type: "error", reason: ErrorCodes.INVALID_DYNAMIC_KEY});
          that.dispatchEvent(evt);
        } else if (err === ErrorCodes.ERR_DYNAMIC_KEY_TIMEOUT) {
          var evt = StreamEvent({type: "error", reason: ErrorCodes.DYNAMIC_KEY_TIMEOUT});
          that.dispatchEvent(evt);
          var time = generateInterval(that.attemps);
          Logger.error("Connect to server failed [Channel key timeout], attempt to recover [#" + that.attemps + "] after " + time / 1000 + " seconds");
          var timer = setTimeout(function () {
            that.attemps++;
            if (that.socket !== undefined) {
              that.socket.disconnect();
            }
          }, time);
        }
      });
    } else {
      Logger.error("Connection recover failed [Invalid channel key]");
    }
  };

  var createSocket = function (host) {
    that.socket = Socket(host, {sid: that.joinInfo.sid});
  };

    var connectSocket = function (joinInfo, callback, error) {
      // Once we have connected
      var max_connect_attempts = 10;
      var connect_timeout_attempt = 0;
      // Deal with transport close
      // Sometimes socket.io will disconnect immediately after connected,
      // we need some strategy to deal with this case.

      // This handles reconnection.
      // If we try to re-initialize "that.socket" each time when connecting,
      // reconnecting after disconnected from gateway could not result in sucess.
      // However, we could simply reuse previous created "that.socket" to
      // re-establish the connection between client and gateway, by calling
      // "that.socket.socket.connect()". And we don"t delete "that.socket"
      // when "client-disconnect" event triggered.
      if (that.socket !== undefined) {
        that.socket.socket.connect();
      } else {
        // Create socket connection.
        createSocket(joinInfo.gatewayAddr);

        that.socket.on("onUplinkStats", function (args) {
          if(that.localStreams[that.uid]) {
            that.localStreams[that.uid].uplinkStats = args;
          }
        });

        // Add connect event listener.
        that.socket.on("connect", function () {
          that.attemps = 1;
          sendMessage(Token(joinInfo), callback, error);
        });

        that.socket.on("reconnect", function () {
          Logger.debug("Try to reconnect choose server and get gateway list again ");
          getGatewayList(that.joinInfo, function(res) {
            that.socket.reconnect(res.gateway_addr);
          })
        });

        // Add connect_error event listener.
        that.socket.on("connect_error", function (err) {
          // Clear peer connection stats timers
          for (var i in that.timers) {
            if (that.timers.hasOwnProperty(i)) {
              clearInterval(that.timers[i]);
            }
          }

          for (var i in that.remoteStreams) {
            if (that.remoteStreams.hasOwnProperty(i)) {
              var stream = that.remoteStreams[i];
              // Dispatch peer leave event
              var evt = ClientEvent({type: "stream-removed", uid: stream.getId(), stream: stream});
              that.dispatchEvent(evt);
            }
          }

          removeRemoteStreams();
          if(isBanned == true) return;

          clearInterval(that.pingTimer);
          that.state = DISCONNECTED;
          that.socket = undefined;
          var evt = StreamEvent({type: "error", reason: ErrorCodes.SOCKET_ERROR});
          that.dispatchEvent(evt);

          var time = generateInterval(that.attemps);
          Logger.error("Connect to server error [" + JSON.stringify(err) + "], attempt to recover [#" + that.attemps + "] after " + time / 1000 + " seconds");
          var timer = setTimeout(function () {
            // We"ve tried to reconnect so increase the attempts by 1
            that.attemps++;
            recover();
          }, time);
        });

        that.socket.on("disconnect", function (reason) {
          if (that.state === DISCONNECTED) return;
          that.state = DISCONNECTED;

          // Clear peer connection stats timers
          for (var i in that.timers) {
            if (that.timers.hasOwnProperty(i)) {
              clearInterval(that.timers[i]);
            }
          }

          for (var i in that.remoteStreams) {
            if (that.remoteStreams.hasOwnProperty(i)) {
              var stream = that.remoteStreams[i];
              // Dispatch peer leave event
              var evt = ClientEvent({type: "stream-removed", uid: stream.getId(), stream: stream});
              that.dispatchEvent(evt);
            }
          }

          removeRemoteStreams();
          if(isBanned == true) return;
          clearInterval(that.pingTimer);
          that.socket = undefined;
          //safeCall(error, ErrorCodes.SOCKET_DISCONNECTED);
          var evt = StreamEvent({type: "error", reason: ErrorCodes.SOCKET_DISCONNECTED});
          that.dispatchEvent(evt);

          var time = generateInterval(that.attemps);
          Logger.error("Disconnect from server [" + reason + "], attempt to recover [#" + that.attemps + "] after " + time / 1000 + " seconds");
          var timer = setTimeout(function () {
            // We"ve tried to reconnect so increment the attempts by 1
            that.attemps++;
            recover();
          }, time);
        });

        // We receive an event with a new stream.
        // type can be "media" or "data"
        that.socket.on("onAddAudioStream", function (arg) {
          if (that.remoteStreams[arg.id] === undefined) {
            var stream = Stream({
              streamID: arg.id, local: false, audio: arg.audio, video: arg.video,
              screen: arg.screen, attributes: arg.attributes});
            that.remoteStreams[arg.id] = stream;
            var evt = StreamEvent({type: "stream-added", stream: stream});
            that.dispatchEvent(evt);
          }
        });

        that.socket.on("onAddVideoStream", function (arg) {
          Logger.info("Newly added remote stream with uid", arg.id);
          if (that.remoteStreams[arg.id] === undefined) {
            var stream = Stream({
              streamID: arg.id, local: false, audio: arg.audio, video: arg.video,
              screen: arg.screen, attributes: arg.attributes});
            that.remoteStreams[arg.id] = stream;
            var evt = StreamEvent({type: "stream-added", stream: stream});
            that.dispatchEvent(evt);
          } else {
            if (that.remoteStreams[arg.id].stream !== undefined) {
              that.remoteStreams[arg.id].video = true;
              that.remoteStreams[arg.id].enableVideo();
              Logger.info("Stream changed: enable video " + arg.id);
              var st = that.remoteStreams[arg.id];
              var element = st.player.elementID;
              st.stop();
              st.play(element);
            } else {
              var stream = Stream({
                streamID: arg.id, local: false, audio: true, video: true,
                screen: false, attributes: arg.attributes});
              that.remoteStreams[arg.id] = stream;
              Logger.info("Stream changed: modify video " + arg.id);
            }
          }
        });

        // We receive an event of a stream removed
        that.socket.on("onRemoveStream", function (arg) {
          var stream = that.remoteStreams[arg.id];
          if (!stream) {
            console.log("ERROR stream ", arg.id, " not found onRemoveStream ", arg);
            return;
          }
          delete that.remoteStreams[arg.id];
          var evt = StreamEvent({type: "stream-removed", stream: stream});
          that.dispatchEvent(evt);
          stream.close();
          if (stream.pc !== undefined) {
            stream.pc.close();
            stream.pc = undefined;
          }
        });

        // We receive an event of local stream published
        that.socket.on("onPublishStream", function (arg) {
          var stream = that.localStreams[arg.id];
          var evt = StreamEvent({type: "stream-published", stream: stream});
          that.dispatchEvent(evt);
        });

        that.socket.on("mute_audio", function (arg) {
          Logger.info("rcv peer mute audio");     
          var evt = ClientEvent({type: "mute-audio", uid: arg.peerid});
          that.dispatchEvent(evt);
        });

        that.socket.on("unmute_audio", function (arg) {
          Logger.info("rcv peer unmute audio");  
          var evt = ClientEvent({type: "unmute-audio", uid: arg.peerid});
          that.dispatchEvent(evt);
        });

        that.socket.on("mute_video", function (arg) {
          Logger.info("rcv peer mute video");   
          var evt = ClientEvent({type: "mute-video", uid: arg.peerid});
          that.dispatchEvent(evt);
        });

        that.socket.on("unmute_video", function (arg) {
          Logger.info("rcv peer unmute video");   
          var evt = ClientEvent({type: "unmute-video", uid: arg.peerid});
          that.dispatchEvent(evt);
        }); 
		
        that.socket.on("user_banned", function (arg) {
          Logger.info("user banned uid:"+ arg.id+"error:"+arg.errcode); 
          var evt = ClientEvent({type: "client-banned", uid: arg.id, attr:arg.errcode});
          that.dispatchEvent(evt);
          isBanned = true;
          leave();
        });

        that.socket.on("onP2PLost", function (arg) {
          Logger.debug("p2plost:", arg);
          if(arg.event === 'publish') {
            let stream = that.localStreams[arg.uid];
            stream && report.publish(that.joinInfo.sid, {
              lts: stream.publishLTS,
              succ: false,
              ec: 'DTLS failed',
            });
          }

          if(arg.event === 'subscribe') {
            let stream = that.remoteStreams[arg.uid];
            stream && report.subscribe(that.joinInfo.sid, {
              lts: stream.subscribeLTS,
              succ: false,
              peerid: arg.uid + '',
              ec: 'DTLS failed',
            });
          }

          that.socket.disconnect();
          // var time = generateInterval(that.p2p_attemps) + generateInterval(that.attemps);
          // Logger.error("p2p connection lost, attempt to recover [#" + that.p2p_attemps + "] after " + time / 1000 + " seconds");
          // var timer = setTimeout(function () {
          //   that.p2p_attemps++;
          //   if (that.socket !== undefined) {
          //     that.socket.disconnect();
          //   }
          // }, time);
        });

        // We receive an event of a client leaved
        that.socket.on("onPeerLeave", function (arg) {
          var evt = ClientEvent({type: "peer-leave", uid: arg.id});
          if (that.remoteStreams.hasOwnProperty(arg.id)) {
            evt.stream = that.remoteStreams[arg.id];
          }
          that.dispatchEvent(evt);

          if (that.remoteStreams.hasOwnProperty(arg.id)) {
            Logger.info("closing stream on peer leave", arg.id);
            var stream = that.remoteStreams[arg.id];
            stream.close();
            delete that.remoteStreams[arg.id];
            if (stream.pc !== undefined) {
              stream.pc.close();
              stream.pc = undefined;
            }
          }

          if (that.timers.hasOwnProperty(arg.id)) {
            clearInterval(that.timers[arg.id]);
            delete that.timers[arg.id];
          }

          if (that.audioLevel[arg.id] != undefined) {
            delete(that.audioLevel[arg.id]);
          }

          if (that.timer_counter[arg.id] != undefined) {
            delete(that.timer_counter[arg.id]);
          }
        });

        that.socket.on("onUplinkStats", function (args) {
        });

        that.socket.on("liveStreamingStarted", function (args) {
          var evt = LiveStreamingEvent({type: "liveStreamingStarted", url: args.url});
          that.dispatchEvent(evt);
        });

        that.socket.on("liveStreamingFailed", function (args) {
          var evt = LiveStreamingEvent({type: "liveStreamingFailed", url: args.url});
          that.dispatchEvent(evt);
        });

        that.socket.on("liveStreamingStopped", function (args) {
          var evt = LiveStreamingEvent({type: "liveStreamingStopped", url: args.url});
          that.dispatchEvent(evt);
        });
        that.socket.on("liveTranscodingUpdated", function (args) {
          var evt = LiveStreamingEvent({type: "liveTranscodingUpdated", reason: args.reason});
          that.dispatchEvent(evt);
        });
      }
    };

    var sendMessage = function (message, callback, error) {
      if (that.socket === undefined) {
        safeCall(error, ErrorCodes.INVALID_OPERATION);
        Logger.error("No socket available");
        return;
      }
      try {
        that.socket.emitSimpleMessage(message, function (respType, result) {
          if (respType === "success") {
            if (typeof callback === "function") {
              callback(result);
            }
          } else {
            if (typeof error === "function") {
              error(result);
            }
          }
        });
      } catch (err) {
        safeCall(error, ErrorCodes.SOCKET_ERROR);
        Logger.error("Socket emit message failed" + JSON.stringify(message));
        Logger.error(err);
      }
    };

    // It sends a SDP message to the server using socket.io
    var sendSimpleSdp = function (message, callback) {
      if (that.socket === undefined) {
        Logger.error("Error in sendSimpleSdp [socket not ready]");
        return;
      }
      try {
        that.socket.emitSimpleMessage(message, function (response, respCallback) {
          if (callback !== undefined) {
            callback(response, respCallback);
          }
        });
      } catch (err) {
        Logger.error("Error in sendSimpleSdp [" + err + "]");
      }
    };

    function removeRemoteStreams() {
      for (var i in that.remoteStreams) {
        if (that.remoteStreams.hasOwnProperty(i)) {
          var stream = that.remoteStreams[i];
          stream.stop();
          stream.close();
          delete that.remoteStreams[i];
          if (stream.pc !== undefined) {
            stream.pc.close();
            stream.pc = undefined;
          }
        }
      }
    }

    return that;
};

export default GatewayClient



