var isChrome = function() {
	var info = getBrowserInfo();
	return info.name && info.name === 'Chrome';
}

var isSafari = function() {
	var info = getBrowserInfo()
	return info.name && info.name === 'Safari';
}

var isFireFox = function() {
	var info = getBrowserInfo()
	return info.name && info.name === 'Firefox';
}

var isOpera = function() {
	var info = getBrowserInfo()
	return info.name && info.name === 'OPR';
}

var isQQBrowser = function() {
	var info = getBrowserInfo()
	return info.name && info.name === 'MQQBrowser';
}

var isWeChatBrowser = function() {
	var info = getBrowserInfo()
	return info.name && info.name === 'MicroMessenger';
}

var isSupportedPC = function() {
	var os = getBrowserOS();
	return os === 'Linux' || os === 'Mac OS X' || os === 'Mac OS' || os.indexOf("Windows") !== -1;
}

var isSupportedMobile = function() {
	var os = getBrowserOS();
	return os === 'Android' || os === 'iOS'; 
}

var getBrowserVersion = function() {
	var info = getBrowserInfo();
	return info.version;
}

var getBrowserOS = function() {
	var info = getBrowserInfo();
	return info.os;
}

var _getBrowserInfo = function() {
	var ua=navigator.userAgent,tem,M=ua.match(/(opera|chrome|safari|firefox|msie|trident(?=\/))\/?\s*(\d+)/i) || []; 

	// TODO: copy from Stack OverFlow, I don't think it works
	if(/trident/i.test(M[1])){
		tem=/\brv[ :]+(\d+)/g.exec(ua) || []; 
		return {name:'IE',version:(tem[1]||'')};
	}   

	// for opera
	if(M[1]==='Chrome'){
		tem=ua.match(/(OPR(?=\/))\/?(\d+)/i);
		if(tem!=null) {
			M = tem;
		}
	}

	// for QQ
	if(M[1]==='Chrome'){
		tem=ua.match(/(mqqbrowser(?=\/))\/?(\d+)/i);
		if(tem!=null) {
			M = tem;
		}
	}

	// for weChat
	if(M[1]==='Chrome'){
		tem=ua.match(/(micromessenger(?=\/))\/?(\d+)/i);
		if(tem!=null) {
			M = tem;
		}
	}

	// for Safari version
	if(M[1]==='Safari'){
		if((tem=ua.match(/version\/(\d+)/i))!=null) {
			M[2] = tem[1];
	}
	}

	var os = undefined;
	var clientStrings = [
		{s:'Windows 10', r:/(Windows 10.0|Windows NT 10.0)/},
		{s:'Windows 8.1', r:/(Windows 8.1|Windows NT 6.3)/},
		{s:'Windows 8', r:/(Windows 8|Windows NT 6.2)/},
		{s:'Windows 7', r:/(Windows 7|Windows NT 6.1)/},
		{s:'Windows Vista', r:/Windows NT 6.0/},
		{s:'Windows Server 2003', r:/Windows NT 5.2/},
		{s:'Windows XP', r:/(Windows NT 5.1|Windows XP)/},
		{s:'Windows 2000', r:/(Windows NT 5.0|Windows 2000)/},
		{s:'Windows ME', r:/(Win 9x 4.90|Windows ME)/},
		{s:'Windows 98', r:/(Windows 98|Win98)/},
		{s:'Windows 95', r:/(Windows 95|Win95|Windows_95)/},
		{s:'Windows NT 4.0', r:/(Windows NT 4.0|WinNT4.0|WinNT|Windows NT)/},
		{s:'Windows CE', r:/Windows CE/},
		{s:'Windows 3.11', r:/Win16/},
		{s:'Android', r:/Android/},
		{s:'Open BSD', r:/OpenBSD/},
		{s:'Sun OS', r:/SunOS/},
		{s:'Linux', r:/(Linux|X11)/},
		{s:'iOS', r:/(iPhone|iPad|iPod)/},
		{s:'Mac OS X', r:/Mac OS X/},
		{s:'Mac OS', r:/(MacPPC|MacIntel|Mac_PowerPC|Macintosh)/},
		{s:'QNX', r:/QNX/},
		{s:'UNIX', r:/UNIX/},
		{s:'BeOS', r:/BeOS/},
		{s:'OS/2', r:/OS\/2/},
		{s:'Search Bot', r:/(nuhk|Googlebot|Yammybot|Openbot|Slurp|MSNBot|Ask Jeeves\/Teoma|ia_archiver)/}
	];
	for (var id in clientStrings) {
			var cs = clientStrings[id];
			if (cs.r.test(navigator.userAgent)) {
				os = cs.s;
				break;
			}
	}

	return {
		name: M[1],
		version: M[2],
		os: os 
	};
}

var getBrowserInfo = (function() {
	var info = _getBrowserInfo();
	return function() {
		return info;
	}
})();

export {
	getBrowserInfo,
	getBrowserVersion,
	getBrowserOS,
	isChrome,
	isSafari,
	isFireFox,
	isOpera,
	isQQBrowser,
	isWeChatBrowser,
	isSupportedPC,
	isSupportedMobile
}



