import Logger from '../utils/Logger'

var SafariStableStack = function (spec) {
    "use strict";

    var that = {},
        //WebkitRTCPeerConnection = webkitRTCPeerConnection;
        WebkitRTCPeerConnection = RTCPeerConnection;

    that.isSubscriber = spec.isSubscriber;
    that.pc_config = {
        "iceServers": [{urls: ["stun:72.251.224.27:3478"]}],
        "bundlePolicy": "max-bundle"
    };

    that.con = {'optional': [{'DtlsSrtpKeyAgreement': true}]};

    if (spec.iceServers instanceof Array) {
        that.pc_config.iceServers = spec.iceServers;
    } else {
        if (spec.stunServerUrl) {
            if (spec.stunServerUrl instanceof Array) {
                spec.stunServerUrl.map(function (url) {
                    if (typeof url === 'string' && url !== '') {
                        that.pc_config.iceServers.push({"url": url});
                    }
                });
            } else if (typeof spec.stunServerUrl === 'string' && spec.stunServerUrl !== '') {
                that.pc_config.iceServers.push({"url": spec.stunServerUrl});
            }
        }

        if (spec.turnServer) {
            if (spec.turnServer instanceof Array) {
                spec.turnServer.map(function (turn) {
                    if (typeof turn.url === 'string' && turn.url !== '') {
                        that.pc_config.iceServers.push({
                            "username": turn.username,
                            "credential": turn.password,
                            "url": turn.url
                        });
                    }
                });
            } else if (typeof spec.turnServer.url === 'string' && spec.turnServer.url !== '') {
                that.pc_config.iceServers.push({
                    "username": spec.turnServer.username,
                    "credential": spec.turnServer.password,
                    "url": spec.turnServer.url
                });
            }
        }
    }

    if (spec.audio === undefined) {
        spec.audio = true;
    }

    if (spec.video === undefined) {
        spec.video = true;
    }

    that.mediaConstraints = {
        'mandatory': {
            'OfferToReceiveVideo': spec.video,
            'OfferToReceiveAudio': spec.audio
        }
    };

    that.roapSessionId = 103;

    that.peerConnection = new RTCPeerConnection({"iceServers": [{urls: ["stun:webcs.agora.io:3478", "stun:stun.l.google.com:19302"]}], "bundlePolicy": "max-bundle"});

    that.peerConnection.onicecandidate = function (event) {
        Logger.debug("PeerConnection: ", spec.session_id, event);
        if (!event.candidate) {
            // At the moment, we do not renegotiate when new candidates
            // show up after the more flag has been false once.
            Logger.debug("PeerConnection State: " + that.peerConnection.iceGatheringState);

	    clearTimeout(that.timeout);
            if (that.ices === undefined) {
                that.ices = 0;
            }
            that.ices = that.ices + 1;
            if (that.ices >= 1 && that.moreIceComing) {
                that.moreIceComing = false;
                that.markActionNeeded();
            }
        } else {
	    //Logger.debug(event.candidate);
	    if (that.iceCandidateCount === 0) {
	      that.timeout = setTimeout(function() {
                if (that.moreIceComing) {
                  that.moreIceComing = false;
		  that.markActionNeeded();
	        }
	      }, 1000);
	    }
	    that.iceCandidateCount = that.iceCandidateCount + 1;
        }
    };

    //Logger.debug("Created webkitRTCPeerConnnection with config \"" + JSON.stringify(that.pc_config) + "\".");

    var filterSdp = function (sdp) {
      var a, r;
      if (spec.screen) {
        sdp = sdp.replace("a=x-google-flag:conference\r\n", '');
      }
      return sdp;
    }

    var setMaxBW = function (sdp) {
        var a, r;
        if (spec.minVideoBW && spec.maxVideoBW) {
            a = sdp.match(/m=video.*\r\n/);
            r = a[0] + "b=AS:" + spec.maxVideoBW + "\r\n";
            //if (spec.mode === 'live') { //h.264
            //  r = a[0] + "a=fmtp:107 x-google-min-bitrate=" + spec.minVideoBW + "; x-google-max-bitrate=" + spec.maxVideoBW + "\r\n";
            //} else { //vp8
            //  r = a[0] + "a=fmtp:100 x-google-min-bitrate=" + spec.minVideoBW + "; x-google-max-bitrate=" + spec.maxVideoBW + "\r\n";
            //}
            sdp = sdp.replace(a[0], r);
            Logger.debug('Set Video Bitrate - min:' + spec.minVideoBW + " max:" + spec.maxVideoBW);
        }

        if (spec.maxAudioBW) {
            a = sdp.match(/m=audio.*\r\n/);
            r = a[0] + "b=AS:" + spec.maxAudioBW + "\r\n";
            sdp = sdp.replace(a[0], r);
        }

        return sdp;
    };

    /**
     * This function processes signalling messages from the other side.
     * @param {string} msgstring JSON-formatted string containing a ROAP message.
     */
    that.processSignalingMessage = function (msgstring) {
        // Offer: Check for glare and resolve.
        // Answer/OK: Remove retransmit for the msg this is an answer to.
        // Send back "OK" if this was an Answer.
        //Logger.debug('Activity on conn ' + that.sessionId);
        var msg = JSON.parse(msgstring), sd;
        that.incomingMessage = msg;

        if (that.state === 'new') {
            if (msg.messageType === 'OFFER') {
                // Initial offer.
                sd = {
                    sdp: msg.sdp,
                    type: 'offer'
                };
                that.peerConnection.setRemoteDescription(new RTCSessionDescription(sd));

                that.state = 'offer-received';
                // Allow other stuff to happen, then reply.
                that.markActionNeeded();
            } else {
                that.error('Illegal message for this state: ' + msg.messageType + ' in state ' + that.state);
            }

        } else if (that.state === 'offer-sent') {
            if (msg.messageType === 'ANSWER') {

                sd = {
                    sdp: msg.sdp,
                    type: 'answer'
                };

                //Logger.debug("Received ANSWER: ", sd.sdp);
                sd.sdp = filterSdp(sd.sdp);
                sd.sdp = setMaxBW(sd.sdp);
                sd.sdp = sd.sdp.replace(/a=x-google-flag:conference\r\n/g, "");

                that.peerConnection.setRemoteDescription(new RTCSessionDescription(sd));
                that.sendOK();
                that.state = 'established';

            } else if (msg.messageType === 'pr-answer') {
                sd = {
                    sdp: msg.sdp,
                    type: 'pr-answer'
                };
                that.peerConnection.setRemoteDescription(new RTCSessionDescription(sd));

                // No change to state, and no response.
            } else if (msg.messageType === 'offer') {
                // Glare processing.
                that.error('Not written yet');
            } else {
                that.error('Illegal message for this state: ' + msg.messageType + ' in state ' + that.state);
            }

        } else if (that.state === 'established') {
            if (msg.messageType === 'OFFER') {
                // Subsequent offer.
                sd = {
                    sdp: msg.sdp,
                    type: 'offer'
                };
                that.peerConnection.setRemoteDescription(new RTCSessionDescription(sd));

                that.state = 'offer-received';
                // Allow other stuff to happen, then reply.
                that.markActionNeeded();
            } else {
                that.error('Illegal message for this state: ' + msg.messageType + ' in state ' + that.state);
            }
        }
    };

    var outbound_rtp_audio = {
      id: "",
      type: "",
      mediaType: "",
      googCodecName: "opus",
      aecDivergentFilterFraction: "0",
      audioInputLevel: "0",
      bytesSent: "0",
      packetsSent: "0",
      googEchoCancellationReturnLoss: "0",
      googEchoCancellationReturnLossEnhancement: "0"
    };

    var outbound_rtp_video = {
      id: "",
      type: "",
      mediaType: "",
      googCodecName: spec.codec === "h264" ? "H264" : "VP8",
      bytesSent: "0",
      packetsLost: "0",
      packetsSent: "0",
      googAdaptationChanges: "0",
      googAvgEncodeMs: "0",
      googEncodeUsagePercent: "0",
      googFirsReceived: "0",
      googFrameHeightSent: "0",
      googFrameHeightInput: "0",
      googFrameRateInput: "0",
      googFrameRateSent: "0",
      googFrameWidthSent: "0",
      googFrameWidthInput: "0",
      googNacksReceived: "0",
      googPlisReceived: "0",
      googRtt: "0",
      googFramesEncoded: "0",
    };

    var inbound_rtp_audio = {
      id: "",
      type: "",
      mediaType: "",
      audioOutputLevel: "0",
      bytesReceived: "0",
      packetsLost: "0",
      packetsReceived: "0",
      googAccelerateRate: "0",
      googCurrentDelayMs: "0",
      googDecodingCNG: "0",
      googDecodingCTN: "0",
      googDecodingCTSG: "0",
      googDecodingNormal: "0",
      googDecodingPLC: "0",
      googDecodingPLCCNG: "0",
      googExpandRate: "0",
      googJitterBufferMs: "0",
      googJitterReceived: "0",
      googPreemptiveExpandRate: "0",
      googPreferredJitterBufferMs: "0",
      googSecondaryDecodedRate: "0",
      googSpeechExpandRate: "0"
    };

    var inbound_rtp_video = {
      id: "",
      type: "",
      mediaType: "",
      googTargetDelayMs: "0",
      packetsLost: "0",
      googDecodeMs: "0",
      googMaxDecodeMs: "0",
      googRenderDelayMs: "0",
      googFrameWidthReceived: "0",
      googFrameHeightReceived: "0",
      googFrameRateReceived: "0",
      googFrameRateDecoded: "0",
      googFrameRateOutput: "0",
      googFramesDecoded: "0",
      googFrameReceived: "0",

      googJitterBufferMs: "0",
      googCurrentDelayMs: "0",
      googMinPlayoutDelayMs: "0",
      googNacksSent: "0",
      googPlisSent: "0",
      googFirsSent: "0",
      bytesReceived: "0",
      packetsReceived: "0",
    };

    var bweforvideo = {
      id: "bweforvideo",
      type: "VideoBwe",
      googAvailableSendBandwidth: "0",
      googAvailableReceiveBandwidth: "0",
      googActualEncBitrate: "0",
      googRetransmitBitrate: "0",
      googTargetEncBitrate: "0",
      googBucketDelay: "0",
      googTransmitBitrate: "0",
    }

    var lastFramesEncoded = 0;
    var lastFramesDecoded = 0;
    var lastFramesReceived = 0;


    that.getStatsRate = function(callback) {
      that.getStats(function(stats) {
        stats.forEach(function(report) {
            if (report.type === 'outbound-rtp' && report.mediaType === 'video') {
                if(report.googFramesEncoded) {
                    report.googFrameRateSent = ((report.googFramesEncoded - lastFramesEncoded) / 3).toString();
                    lastFramesEncoded = report.googFramesEncoded;
                }
            }
            if (report.type === 'inbound-rtp' && report.id.indexOf("55543") != -1) {
                // if googFrameRateReceived or googFrameRateDecoded is "null" or "undefined", it will sent "NAN" to server and make server crash
                if(report.googFrameRateReceived) {
                    report.googFrameRateReceived = ((report.googFrameReceived - lastFramesReceived) / 3).toString();
                    lastFramesReceived = report.googFrameReceived;
                }
                if(report.googFrameRateDecoded) {
                    report.googFrameRateDecoded = ((report.googFramesDecoded - lastFramesDecoded) / 3).toString();
                    lastFramesDecoded = report.googFramesDecoded;
                }
            }
        })
        callback(stats);
      });
    }

    that.getStats = function(callback) {
        var originList = [];
      that.peerConnection.getStats().then(function (stats) {
        var list = [];
        stats.forEach(report => {
            originList.push(report);
            if (report.type === 'outbound-rtp' && report.mediaType === 'audio') {
                outbound_rtp_audio.id = report.id;
                outbound_rtp_audio.type = report.type;
                outbound_rtp_audio.mediaType = report.mediaType;
                outbound_rtp_audio.bytesSent = report.bytesSent.toString();
                outbound_rtp_audio.packetsSent = report.packetsSent.toString();
            }
            if (report.type === 'outbound-rtp' && report.mediaType === 'video') {
                outbound_rtp_video.id = report.id;
                outbound_rtp_video.type = report.type;
                outbound_rtp_video.mediaType = report.mediaType;
                outbound_rtp_video.bytesSent = report.bytesSent.toString();
                outbound_rtp_video.packetsSent = report.packetsSent.toString();
                outbound_rtp_video.googPlisReceived = report.pliCount.toString();
                outbound_rtp_video.googNacksReceived = report.nackCount.toString();
                outbound_rtp_video.googFirsReceived = report.firCount.toString();
                outbound_rtp_video.googFramesEncoded = report.framesEncoded.toString();
            }
            if (report.type === 'inbound-rtp' && report.id.indexOf("44444") != -1) {
                inbound_rtp_audio.id = report.id;
                inbound_rtp_audio.type = report.type;
                inbound_rtp_audio.mediaType = "audio";
                inbound_rtp_audio.packetsReceived = report.packetsReceived.toString();
                inbound_rtp_audio.bytesReceived = report.bytesReceived.toString();
                inbound_rtp_audio.packetsLost = report.packetsLost.toString();
                inbound_rtp_audio.packetsReceived = report.packetsReceived.toString();
                inbound_rtp_audio.googJitterReceived = report.jitter.toString();
            }
            if (report.type === 'inbound-rtp' && report.id.indexOf("55543") != -1) {
                inbound_rtp_video.id = report.id;
                inbound_rtp_video.type = report.type;
                inbound_rtp_video.mediaType = "video";
                inbound_rtp_video.packetsReceived = report.packetsReceived.toString();
                inbound_rtp_video.bytesReceived = report.bytesReceived.toString();
                inbound_rtp_video.packetsLost = report.packetsLost.toString();
                inbound_rtp_video.googJitterBufferMs = report.jitter.toString();
                inbound_rtp_video.googNacksSent = report.nackCount.toString();
                inbound_rtp_video.googPlisSent = report.pliCount.toString();
                inbound_rtp_video.googFirsSent = report.firCount.toString();
            }
            if (report.type === 'track' && report.id.indexOf("55543") != -1) {
                inbound_rtp_video.googFrameWidthReceived = report.frameWidth.toString();
                inbound_rtp_video.googFrameHeightReceived = report.frameHeight.toString();
                inbound_rtp_video.googFrameReceived = report.framesReceived.toString();
                inbound_rtp_video.googFramesDecoded = report.framesDecoded.toString();                
            } 
            if (report.type === 'track' && report.id.indexOf("44444") != -1) {
                inbound_rtp_audio.audioOutputLevel = report.audioLevel + "";
                outbound_rtp_audio.audioInputLevel = report.audioLevel + "";
            }
            if (report.type === 'candidate-pair') {
                if(report.availableIncomingBitrate == 0) {
                    bweforvideo.googAvailableSendBandwidth = report.availableOutgoingBitrate + "";
                } else {
                    bweforvideo.googAvailableReceiveBandwidth = report.availableIncomingBitrate + "";
                }
            }
        });
        var filterList = [bweforvideo, outbound_rtp_audio, outbound_rtp_video, inbound_rtp_audio, inbound_rtp_video];
        filterList.push({
            id: "time",
            startTime: that.connectedTime,
            timestamp: new Date(),
        })
        callback(filterList, originList);
      }).catch(function (error) {
          console.error(error);
      });
    };

    /**
     * Adds a stream - this causes signalling to happen, if needed.
     * @param {MediaStream} stream The outgoing MediaStream to add.
     */
    that.addStream = function (stream) {
        if (window.navigator.userAgent.indexOf("Safari") > -1 && navigator.userAgent.indexOf("Chrome") === -1) {
          stream.getTracks().forEach(track => that.peerConnection.addTrack(track, stream));
        } else {
          that.peerConnection.addStream(stream);
        }
        that.markActionNeeded();
    };

    /**
     * Removes a stream.
     * @param {MediaStream} stream The MediaStream to remove.
     */
    that.removeStream = function () {
//        var i;
//        for (i = 0; i < that.peerConnection.localStreams.length; ++i) {
//            if (that.localStreams[i] === stream) {
//                that.localStreams[i] = null;
//            }
//        }
        that.markActionNeeded();
    };

    /**
     * Closes the connection.
     */
    that.close = function () {
        that.state = 'closed';
        that.peerConnection.close();
    };

    /**
     * Internal function: Mark that something happened.
     */
    that.markActionNeeded = function () {
        that.actionNeeded = true;
        that.doLater(function () {
            that.onstablestate();
        });
    };

    /**
     * Internal function: Do something later (not on this stack).
     * @param {function} what Callback to be executed later.
     */
    that.doLater = function (what) {
        // Post an event to myself so that I get called a while later.
        // (needs more JS/DOM info. Just call the processing function on a delay
        // for now.)
        window.setTimeout(what, 1);
    };

    /**
     * Internal function called when a stable state
     * is entered by the browser (to allow for multiple AddStream calls or
     * other interesting actions).
     * This function will generate an offer or answer, as needed, and send
     * to the remote party using our onsignalingmessage function.
     */
    that.onstablestate = function () {
        var mySDP;
        if (that.actionNeeded) {
            if (that.state === 'new' || that.state === 'established') {
                // See if the current offer is the same as what we already sent.
                // If not, no change is needed.

                if (spec.isSubscriber && window.navigator.userAgent.indexOf("Safari") > -1 && navigator.userAgent.indexOf("Chrome") === -1) {
                    var audioReceiver = that.peerConnection.addTransceiver("audio");
                    var videoReceiver = that.peerConnection.addTransceiver("video");
                    audioReceiver.setDirection("recvonly");
                    videoReceiver.setDirection("recvonly");
                }
                that.peerConnection.createOffer(that.mediaConstraints).then(function (sessionDescription) {
                    sessionDescription.sdp = setMaxBW(sessionDescription.sdp);

                    if(!spec.isSubscriber) {
                        // Solve the video orientation issue between iOS Safari and Firefox.
                        sessionDescription.sdp = sessionDescription.sdp.replace(/a=extmap:4 urn:3gpp:video-orientation\r\n/g, "");
                    }
                    
                    //Logger.debug("Offer", sessionDescription.sdp);

                    var newOffer = sessionDescription.sdp;

                    if (newOffer !== that.prevOffer) {

                        that.peerConnection.setLocalDescription(sessionDescription);

                        that.state = 'preparing-offer';
                        that.markActionNeeded();
                        return;
                    } else {
                        Logger.debug('Not sending a new offer');
                    }

                }).catch(function(err) {
                    Logger.debug('peer connection create offer failed ', err);
                });


            } else if (that.state === 'preparing-offer') {
                // Don't do anything until we have the ICE candidates.
                if (that.moreIceComing) {
                    return;
                }


                // Now able to send the offer we've already prepared.
                that.prevOffer = that.peerConnection.localDescription.sdp;
                //Logger.debug("Sending OFFER: " + that.prevOffer);
                that.sendMessage('OFFER', that.prevOffer);
                // Not done: Retransmission on non-response.
                that.state = 'offer-sent';

            } else if (that.state === 'offer-received') {

                that.peerConnection.createAnswer(function (sessionDescription) {
                    that.peerConnection.setLocalDescription(sessionDescription);
                    that.state = 'offer-received-preparing-answer';

                    if (!that.iceStarted) {
                        var now = new Date();
                        Logger.debug(now.getTime() + ': Starting ICE in responder');
                        that.iceStarted = true;
                    } else {
                        that.markActionNeeded();
                        return;
                    }

                }, function(err) {
                    Logger.debug('peer connection create answer failed ', err);
                }, that.mediaConstraints);

            } else if (that.state === 'offer-received-preparing-answer') {
                if (that.moreIceComing) {
                    return;
                }

                mySDP = that.peerConnection.localDescription.sdp;

                that.sendMessage('ANSWER', mySDP);
                that.state = 'established';
            } else {
                that.error('Dazed and confused in state ' + that.state + ', stopping here');
            }
            that.actionNeeded = false;
        }
    };

    /**
     * Internal function to send an "OK" message.
     */
    that.sendOK = function () {
        that.sendMessage('OK');
    };

    /**
     * Internal function to send a signalling message.
     * @param {string} operation What operation to signal.
     * @param {string} sdp SDP message body.
     */
    that.sendMessage = function (operation, sdp) {
        var roapMessage = {};
        roapMessage.messageType = operation;
        roapMessage.sdp = sdp; // may be null or undefined
        if (operation === 'OFFER') {
            roapMessage.offererSessionId = that.sessionId;
            roapMessage.answererSessionId = that.otherSessionId; // may be null
            roapMessage.seq = (that.sequenceNumber += 1);
            // The tiebreaker needs to be neither 0 nor 429496725.
            roapMessage.tiebreaker = Math.floor(Math.random() * 429496723 + 1);
        } else {
            roapMessage.offererSessionId = that.incomingMessage.offererSessionId;
            roapMessage.answererSessionId = that.sessionId;
            roapMessage.seq = that.incomingMessage.seq;
        }
        that.onsignalingmessage(JSON.stringify(roapMessage));
    };

    /**
     * Internal something-bad-happened function.
     * @param {string} text What happened - suitable for logging.
     */
    that.error = function (text) {
        throw 'Error in RoapOnJsep: ' + text;
    };

    that.sessionId = (that.roapSessionId += 1);
    that.sequenceNumber = 0; // Number of last ROAP message sent. Starts at 1.
    that.actionNeeded = false;
    that.iceStarted = false;
    that.moreIceComing = true;
    that.iceCandidateCount = 0;
    that.onsignalingmessage = spec.callback;

    that.peerConnection.ontrack = function (evt) {
        if (that.onaddstream) {
            that.onaddstream(evt, "ontrack");
        }
    };

    that.peerConnection.onremovestream = function (stream) {
        if (that.onremovestream) {
            that.onremovestream(stream);
        }
    };

    that.peerConnection.oniceconnectionstatechange = function (e) {
        if(e.currentTarget.iceConnectionState === "connected") {
            that.connectedTime = new Date();
        }

        if (that.oniceconnectionstatechange) {
            that.oniceconnectionstatechange(e.currentTarget.iceConnectionState);
        }
    };

    // Variables that are part of the public interface of PeerConnection
    // in the 28 January 2012 version of the webrtc specification.
    that.onaddstream = null;
    that.onremovestream = null;
    that.state = 'new';
    // Auto-fire next events.
    that.markActionNeeded();
    return that;
};

export default SafariStableStack



