var publishStatsFilter = function(stats) {
  var detection = {
    audioSendBytes: "0",
    audioSendPackets: "0",

    videoSendBytes: "0",
    videoSendPackets: "0",
    videoSendPacketsLost: "0",
    videoSendFrameRate: "0",
    videoSendBandwidth: "0",
    videoSendResolutionWidth: "0",
    videoSendResolutionHeight: "0",

    audioCodecName:"",
    videoCodecName:"",

    timestamp: "",
    startTime: "",
    duration: "0",
  }

  stats.forEach(function(stat) {
    if(stat.type === "VideoBwe") {
      detection.videoSendBandwidth = stat.googAvailableSendBandwidth;

    } else if(stat.id.indexOf("send") !== -1 || 
      stat.id.indexOf("outbound_rtp") !== -1 ||
      stat.id.indexOf("OutboundRTP") !== -1 ) {

      if(stat.mediaType === "audio") {
        detection.audioSendBytes = stat.bytesSent;
        detection.audioSendPackets = stat.packetsSent;
        detection.audioCodecName = stat.googCodecName;
      } else {
        detection.videoSendBytes = stat.bytesSent;
        detection.videoSendPackets = stat.packetsSent;
        detection.videoSendPacketsLost = stat.packetsLost;
        detection.videoSendFrameRate = stat.googFrameRateSent;
        detection.videoSendResolutionWidth = stat.googFrameWidthSent;
        detection.videoSendResolutionHeight = stat.googFrameHeightSent;
        detection.videoCodecName = stat.googCodecName;
      }

    } else if(stat.id === "time") {
      detection.timestamp = stat.timestamp;
      detection.startTime = stat.startTime;
    }
  });
  
  if(detection.timestamp instanceof Date && detection.startTime instanceof Date) {
    detection.duration = Math.floor((detection.timestamp.getTime() - detection.startTime.getTime()) / 1000) + "";
  }

  return detection;
}

var subscribeStatsFilter = function(stats) {
  var detection = {
    audioReceiveBytes: "0",
    audioReceivePackets: "0",
    audioReceivePacketsLost: "0",

    videoReceiveBytes: "0",
    videoReceivePackets: "0",
    videoReceivePacketsLost: "0",
    videoReceiveFrameRate: "0",
    videoReceiveDecodeFrameRate: "0",
    videoReceiveBandwidth: "0",
    videoReceivedResolutionWidth: "0",
    videoReceivedResolutionHeight: "0",

    timestamp: "",
    startTime: "",
    duration: "0",
  }

  stats.forEach(function(stat) {
    if(stat.type === "VideoBwe") {
      detection.videoReceiveBandwidth = stat.googAvailableReceiveBandwidth;

    } else if(stat.id.indexOf("recv") !== -1 || 
      stat.id.indexOf("inbound_rtp") !== -1 ||
      stat.id.indexOf("InboundRTP") !== -1) {

      if(stat.mediaType === "audio") {
        detection.audioReceiveBytes = stat.bytesReceived;
        detection.audioReceivePackets = stat.packetsReceived;
        detection.audioReceivePacketsLost = stat.packetsLost;
      } else {  
        detection.videoReceiveBytes = stat.bytesReceived;
        detection.videoReceivePacketsLost = stat.packetsLost;
        detection.videoReceivePackets = stat.packetsReceived;
        detection.videoReceiveFrameRate = stat.googFrameRateReceived;
        detection.videoReceiveDecodeFrameRate = stat.googFrameRateDecoded;
        detection.videoReceivedResolutionWidth = stat.googFrameWidthReceived;
        detection.videoReceivedResolutionHeight = stat.googFrameHeightReceived;
      }

    } else if(stat.id === "time"){
      detection.timestamp = stat.timestamp;
      detection.startTime = stat.startTime;
    }
  });
  if(detection.timestamp instanceof Date && detection.startTime instanceof Date) {
    detection.duration = Math.floor((detection.timestamp.getTime() - detection.startTime.getTime()) / 1000) + "";
  }

  return detection;  
}

export {
  publishStatsFilter,
  subscribeStatsFilter
}


