import * as Browser from './utils/Browser';
import Logger from './utils/Logger';
import uuidv4 from 'uuid/v4';

var checkSystemRequirements = function() {
var PC = window.RTCPeerConnection || window.mozRTCPeerConnection || window.webkitRTCPeerConnection;
var getUserMedia = navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.msGetUserMedia || navigator.mozGetUserMedia || (navigator.mediaDevices && navigator.mediaDevices.getUserMedia);
var webSocket = window.WebSocket;
  var isAPISupport = !!PC && !!getUserMedia && !!webSocket;

	var isVendorAndVersionSupport = false;
	Logger.debug(Browser.getBrowserInfo(), "isAPISupport:"+isAPISupport);
	
	if(Browser.isChrome() && Browser.getBrowserVersion() >= 58) {
		if (Browser.getBrowserOS() !== 'iOS') {
			isVendorAndVersionSupport = true;
		}
	}

  if(Browser.isFireFox() && Browser.getBrowserVersion() >= 56) {
    isVendorAndVersionSupport = true;
  }

  if(Browser.isOpera() && Browser.getBrowserVersion() >= 45) {
    isVendorAndVersionSupport = true;
  }

  if(Browser.isSafari() && Browser.getBrowserVersion() >= 11) {
    isVendorAndVersionSupport = true;
  }

  // if(Browser.isWeChatBrowser() || Browser.isQQBrowser()) {
  //   isVendorAndVersionSupport = true;
  // }

  if (!Browser.isSupportedPC() && !Browser.isSupportedMobile()) {
    isVendorAndVersionSupport = false;
  }

return isAPISupport && isVendorAndVersionSupport;
}

var safeCall = function() {
  var callback = arguments[0];
  if (typeof callback === "function") {
    var args = Array.prototype.slice.call(arguments, 1);
    callback.apply(null, args);
  }
};

const audioContext = new (window.AudioContext || window.webkitAudioContext)();
var audioLevelHelper = function(stream){
  this.audioContext = audioContext;
  this.sourceNode = stream.otWebkitAudioSource || this.audioContext.createMediaStreamSource(stream);
  this.analyser = this.audioContext.createAnalyser();
  this.timeDomainData = new Uint8Array(this.analyser.frequencyBinCount);
  this.sourceNode.connect(this.analyser);

  this.getAudioLevel = function() {
    if (this.analyser) {
      this.analyser.getByteTimeDomainData(this.timeDomainData);

      // varies from 0 to 255
      let max = 0;
      for (let idx = 0; idx < this.timeDomainData.length; idx++) {
        max = Math.max(max, Math.abs(this.timeDomainData[idx] - 128));
      }

      // normalize the collected level to match the range delivered by
      // the getStats' audioOutputLevel
      return max / 128;
    } else {
      Logger.warning("can't find analyser in audioLevelHelper");
      return 0;
    }
  }
  return this;
}

function generateSessionId() {
  return uuidv4().replace(/-/g,"").toUpperCase();
  // return 'xxxxxxxxxxxxxx4xxxxyxxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
  //   var r = Math.random() * 16 | 0,
  //   v= c=='x' ? r : r & 0x3 | 0x8;
  //   return v.toString(16).toUpperCase();
  // });
}


var CSCrashRecord = {
  shouldBlock: function() {
    if(localStorage) {
      var record = JSON.parse(localStorage.getItem("agoraCSCrashRecord"));
      return record && record.length > 6 ? (new Date().getTime() - record[record.length - 6] - 10000) < 0 ? true : false : false;
    } else {
      return false;
    }
  },
  record: function(date) {
    if(localStorage) {
      var record = JSON.parse(localStorage.getItem("agoraCSCrashRecord"));
      record = !record || record.length > 50 ? [] : record;
      record.push(date);
      localStorage.setItem("agoraCSCrashRecord", JSON.stringify(record));
    }
  }
}

var vsResHack = function(stream, onSuccess, onFailure) {
  try {
    let video = document.createElement('video');
    video.setAttribute('autoplay', '');
    video.setAttribute('muted', '');
    // in iOS, not this attribute will not trigger 'playing' event
    video.setAttribute('playsinline', '');
    video.setAttribute('style', 'width:1px;high:1px;');
    document.body.appendChild(video);
    
    video.addEventListener('playing', ev => {
      // Firefox will triger this event twice
      if(Browser.isFireFox()) {
        if(video.videoWidth) {
          onSuccess(video.videoWidth, video.videoHeight);
          document.body.removeChild(video);
        }
      } else {
        onSuccess(video.videoWidth, video.videoHeight);
        document.body.removeChild(video);
      }
    });
    video.srcObject = stream;
  } catch (error) {
    onFailure(error);
  }
}


export {
  checkSystemRequirements, 
  generateSessionId,
  audioLevelHelper,
  CSCrashRecord,
  vsResHack,
  safeCall
} 



